import { useState, useEffect } from "react";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import axios from "axios";

import LoadingComp from "../../components/Loading";
import SideFilter from "../../components/Filter/SideFilter";
import SearchFilter from "../../components/SearchFilter/SearchFilter";
import ProductsRender from "../../components/Products";
import styles from "./Search.module.css";
import Pagination from "../../components/Pagination/Pagination"

function Search() {
  const [Products, setProducts] = useState([]);
  const [FilterItems, setFilterItems] = useState([])
  const [Loading, setLoading] = useState(true);
  const [searchParams, setSearchParams] = useSearchParams();
  const location = useLocation();
  const [Filters, setFilters] = useState({limit: 20, page: 1});
  const [TotalPage, setTotalPage] = useState(1)

  useEffect(() => {
    window.scrollTo(0, 0);

    const searchRequestURL = `http://localhost/server/search.php${location.search}`
    const categoryRequestURL = "http://localhost/server/product-category.php"
    const manufacturerRequestURL = "http://localhost/server/manufacturers.php"
    const searchRequest = axios.get(searchRequestURL)
    const categoryRequest = axios.get(categoryRequestURL)
    const manufacturersRequest = axios.get(manufacturerRequestURL)

    axios.all([searchRequest, categoryRequest, manufacturersRequest])
    .then(axios.spread((...response) => {
      const ProductResult = response[0].data;
      let CategoryResult = response[1].data;
      let ManufacturerResult = response[2].data;
    
      if(ProductResult.SUCCESS) {
        setProducts(ProductResult.PRODUCTS)
        setTotalPage(ProductResult.LENGTH)
        CategoryResult = CategoryResult.map(Category => ({ID: Category.CATEGORY_ID, NAME: Category.CATEGORY_NAME}))
        ManufacturerResult = ManufacturerResult.map(Category => ({ID: Category.MANUFACTURER_ID, NAME: Category.MANUFACTURER_NAME}))
        setFilterItems([{Type: "category", Title: "Danh mc", Categories: [...CategoryResult]}, {Type: "brands", Title: "Nhn hiu", Categories: [...ManufacturerResult]}])
        setLoading(false)
      }
    }))
  }, [searchParams]);

  const handleChangeCurrentFilter = (filter_name, filter_value) => {
    const newFilter = {
      ...Filters,
      q: searchParams.get("q"),
      page: 1,
      [filter_name]: filter_value,
    };
    setFilters(newFilter);
    setSearchParams(newFilter);
  };

  const setFilterPrice = () => {
    const minPrice = document.querySelector("input[name='minPrice']").value;
    const maxPrice = document.querySelector("input[name='maxPrice']").value;
    const newFilter = {
      ...Filters,
      q: searchParams.get("q"),
      minPrice: minPrice,
      maxPrice: maxPrice,
    };
    setFilters(newFilter);
    setSearchParams(newFilter);
  };

  const handleChangeCheckbox = (data) => {
    let newFilter;
    console.log(data);
    data = data.reduce(
      (acc, val) => ({
          ...acc,
          [val.Type]: val.Checked.join(",")
      }), {});
    if(Object.keys(data).length === 0) {
      let temp = Filters
      if(temp?.category) {
        delete temp.category
      } 
      if(temp?.brands) {
        delete temp.brands
      }
    }
    newFilter = { ...Filters, q: searchParams.get("q") , ...data };
    setFilters(newFilter);
    setSearchParams(newFilter);
  };

  const handleClearFilter = () => {
      setFilters({q: searchParams.get("q")});
      setSearchParams({q: searchParams.get("q")})
  }

  return (
    <div className={styles.section_container}>
      <div className="grid wide">
        <div className="row">
          <div className="col c-2">
            <SearchFilter
              Filters={FilterItems}
              handleChangeCurrentFilter={handleChangeCurrentFilter}
              setFilterPrice={setFilterPrice}
              handleChangeCheckbox={handleChangeCheckbox}
              handleClearFilter={handleClearFilter}
            />
          </div>
          <div className="col c-10">
            <SideFilter handleChangeCurrentFilter={handleChangeCurrentFilter} />
            <div className={styles.container}>
              {Loading ? (
                <LoadingComp />
              ) : Products.length !== 0 ? (
                <>
                  <ProductsRender products={Products} />
                  <Pagination 
                    productsPerPage={Filters.limit}
                    totalProductsLength={TotalPage}
                    handleChangeCurrentFilter={handleChangeCurrentFilter}
                    currentPage={Filters.page}
                    isLoading={Loading}
                  />
                </>
              ) : (
                <div className={styles.empty_search_container}>
                  <div className={styles.empty_search}>
                    <div
                      className={styles.image}
                      style={{
                        backgroundImage:
                        `url(${process.env.PUBLIC_URL}/resources/img/nosearchfound.png)`,
                        backgroundSize: "contain",
                        backgroundRepeat: "no-repeat",
                        backgroundPosition: "50%",
                      }}
                    />
                    <div className={styles.title}>
                      Khng tm thy sn phm ph hp
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Search;
