import { useState, memo} from 'react'

function SideFilter( {handleChangeCurrentFilter: changeCurrentFilter} ) {
    const [currentFilter, setCurrentFilter] = useState('popular')

    const handleChangeCurrentFilter = (filterName, filterValue) => {
        setCurrentFilter(filterValue)
        changeCurrentFilter(filterName, filterValue)
    }

    return (
        <>
            <div className="sidefilter__container">
                <div className="sidefilter__controllers">
                    <div className="sidefilter__tittle">
                        <p>Sp xp theo</p>
                    </div>
                    <div className="sidefilter__controllers-wrapper">
                        <div className={"sidefilter__filter-button btn" + (currentFilter === 'popular'?' btn--primary':'')} onClick={() => handleChangeCurrentFilter('sortBy', 'popular')}>Ph bin</div>
                        <div className={"sidefilter__filter-button btn" + (currentFilter === 'newest'?' btn--primary':'')} onClick={() => handleChangeCurrentFilter('sortBy', 'newest')}>Mi nht</div>
                        <div className="sidefilter__filter-select-box">
                            <span className={['desc', 'asc'].find(filter => filter === currentFilter)?'primary-color':''}>{currentFilter === 'desc'?'Gi: Cao n thp':currentFilter === 'asc'?'Gi: Thp n cao':'Gi'}</span>    
                            <i className="fa-solid fa-angle-down"></i>
                            <div className="sidefilter__filter-select-wrapper">
                                <div className="sidefilter__filter-selector" onClick={() => handleChangeCurrentFilter('sortBy', 'asc')}>Gi: Thp n cao</div>
                                <div className="sidefilter__filter-selector" onClick={() => handleChangeCurrentFilter('sortBy', 'desc')}>Gi: Cao n thp</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>            
        </>
    )
}

export default memo(SideFilter)