import React from "react";

import styles from "./CompletePurchase.module.css";

function CompletePurchase({ type, message, invoice, button1, button2 }) {
  return (
    <div className={styles.modal}>
      <div className={styles.modal_container}>
        <div className={styles.modal_header}>
          <div className={styles.modal_image} style={{backgroundImage: `url(${type==="SUCCESS"?process.env.PUBLIC_URL + '/resources/img/success.png':process.env.PUBLIC_URL + '/resources/img/failure.png'})`, backgroundSize: "contain", backgroundRepeat: "no-repeat"}} />
          <div className={`${styles.hero_title} ${type === "SUCCESS" ? styles.success:styles.failure}`}>{type === "SUCCESS" ? "Thnh cng!":"Tht bi!"}</div>
        </div>
        <div className={styles.modal_body}>
          {message}
        </div>
        <div className={styles.modal_controllers}>
          <button type="button" className="btn" onClick={() => button1()}>Tr v</button>
          <button type="button" className="btn btn--primary" onClick={() => button2()}>Kim tra</button>
        </div>
      </div>
    </div>
  );
}

export default CompletePurchase;
