import { memo } from 'react'
import Product from "./Product/Product"

function Products({ products, filters, productsPerRow }) {

    return (
        <>
            <div className="row">
                {products && products.map(item => (
                    <div className={`col ${productsPerRow?`c-${12/productsPerRow}`:'c-2-4'}`} key={item.PRODUCT_ID}>
                        <Product props={item} />
                    </div>
                ))}
            </div>
        </>
    )
}

export default memo(Products)