import React from "react";
import { useForm } from "react-hook-form";
import { useState, useEffect } from "react";
import axios from "axios";
import styles from "./AdminShop.module.css";

import DeletedStorage from "../../components/DeletedProductsStorage/DeletedProductsStorage";

function AdminShop() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const {
    register: register2,
    handleSubmit: handleSubmit2,
    reset: reset2,
    formState: { errors: errors2 },
  } = useForm();
  const {
    register: register3,
    handleSubmit: handleSubmit3,
    reset: reset3,
    formState: { errors: errors3 },
  } = useForm();
  const [Products, setProducts] = useState([]);
  const [DeletedProducts, setDeletedProducts] = useState([]);
  const [SelectedProductID, setSelectedProductID] = useState("");
  const [ProductModels, setProductModels] = useState([]);
  const [SelectedModelID, setSelectedModelID] = useState(null);
  const [SelectedImg, setSelectedImg] = useState({});
  const [SubItem, setSubItem] = useState({});
  const [Modal, setModal] = useState(false);
  const [IsEditing, setIsEditing] = useState(false);
  const [IsAdding, setIsAdding] = useState(false);
  const [Storage, setStorage] = useState(false);
  const [Popup, setPopup] = useState({
    isOpen: false,
    type: "",
  });

  const openOptionsMenu = (e) => {
    const targetElement = e.target;
    const Product_Control = targetElement.closest(`.${styles.product_control}`);
    const OptionsContaier = Product_Control.querySelector(
      `.${styles.options_container}`
    );

    if (OptionsContaier.classList.contains(`${styles.open}`)) {
      OptionsContaier.classList.remove(`${styles.open}`);
    } else {
      OptionsContaier.classList.add(`${styles.open}`);
    }
  };

  useEffect(() => {
    // fetch to server to get all products
    const getProducts = "http://localhost/server/product.php?getall=1";
    const getCategories = "http://localhost/server/categories.php";
    const getManufacturers = "http://localhost/server/manufacturers.php";

    const requestProducts = axios.get(getProducts);
    const requestCategories = axios.get(getCategories);
    const requestManufacturers = axios.get(getManufacturers);

    axios.all([requestProducts, requestCategories, requestManufacturers]).then(
      axios.spread((...response) => {
        const Products = response[0];
        const Categories = response[1];
        const Manufacturers = response[2];

        setProducts(
          Products.data.PRODUCTS.filter(
            (Product) => Product.IS_DISABLED === "0"
          )
        );
        setDeletedProducts(
          Products.data.PRODUCTS.filter(
            (Product) => Product.IS_DISABLED === "1"
          )
        );
        setSubItem({
          Categories: Categories.data.filter(
            (Category) => Category.IS_DISABLED === "0"
          ),
          Manufacturer: Manufacturers.data,
        });
      })
    );
  }, []);

  // Clear Form input after submit or close Modal
  useEffect(() => {
    reset({
      keepValues: false,
    });
  }, [Modal]);

  // Clear Form input after submit or close Popup
  useEffect(() => {
    reset2({
      keepValues: false,
    });
  }, [Popup]);

  // Clear Adding Form input after submit or close Popup
  useEffect(() => {
    reset3({
      keepValues: false,
    });
  }, [IsAdding]);

  // Close modal when Submit Success
  useEffect(() => {
    setModal(false);
  }, [Products]);

  // Close modal when Submit Success
  useEffect(() => {
    setPopup({
      isOpen: false,
      type: "",
    });
  }, [ProductModels]);

  const UploadImage = (PRODUCT_ID) => {
    const formData = new FormData();
    formData.append("image", SelectedImg.file);
    axios({
      method: "POST",
      url: `http://localhost/server/update-product.php?PRODUCT_ID=${PRODUCT_ID}`,
      data: formData,
      headers: {
        "Content-Type": "multipart/form-data",
      },
    })
      .then((res) => console.log(res.data))
      .catch((err) => console.error("Error: ", err));
  };

  // save Modal Form to State
  const UpdateProduct = (data) => {
    data.PRODUCT_ID = SelectedProductID;
    axios({
      method: "POST",
      url: `http://localhost/server/update-product.php?PRODUCT_ID=${SelectedProductID}`,
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
      .then((response) => {
        const result = response.data;
        if (result.Status === "OK") {
          const newProductArray = Products.map((Product) =>
            Product.PRODUCT_ID === SelectedProductID
              ? SelectedImg
                ? {
                    ...Product,
                    ...data,
                    VARIANTS: ProductModels,
                    PRODUCT_IMAGE: SelectedImg.name,
                  }
                : { ...Product, ...data, VARIANTS: ProductModels }
              : Product
          );
          setProducts(newProductArray);
          setIsEditing(false);

          SelectedImg && UploadImage(SelectedProductID);
        }
      })
      .catch((err) => console.log(err));
  };

  const AddProduct = (data) => {
    const NewProduct = { ...data, PRODUCT_IMAGE: SelectedImg.name };
    setProducts((prev) => [...prev, NewProduct]);
  };

  const UploadProduct = (data) => {
    axios({
      method: "POST",
      url: "http://localhost/server/add-product.php",
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
      .then((res) => {
        const result = res.data; // If failed, server will have a return
        if (result.Status === "OK") {
          console.log("Thnh cng");
          AddProduct(data);
          UploadImage(data.PRODUCT_ID);
          setIsAdding(false);
          setSelectedImg({});
        } else {
          console.log("Tht bi");
        }
      })
      .catch((err) => console.log("Error", err));
  };

  const DeleteProduct = (PRODUCT_ID) => {
    const text = `n sn phm ${PRODUCT_ID}`
    if(window.confirm(text) === true) {
      axios({
        method: "POST",
        url: `http://localhost/server/delete-product.php`,
        data: JSON.stringify({ PRODUCT_ID: PRODUCT_ID, STATUS: "1" }),
        headers: {
          "Content-Type": "multipart/json",
        },
      }).then((response) => {
        const result = response.data;
        if (result.Status === "OK") {
          setProducts(
            Products.filter((Product) => Product.PRODUCT_ID !== PRODUCT_ID)
          );
          setDeletedProducts((prev) => [
            ...prev,
            Products.filter((Product) => Product.PRODUCT_ID === PRODUCT_ID)[0],
          ]);
        } else {
        }
      });
    }
  };

  const CancelDeleteProduct = (PRODUCT_ID) => {
    axios({
      method: "POST",
      url: `http://localhost/server/delete-product.php`,
      data: JSON.stringify({ PRODUCT_ID: PRODUCT_ID, STATUS: "0" }),
      headers: {
        "Content-Type": "multipart/json",
      },
    }).then((response) => {
      const result = response.data;
      if (result.Status === "OK") {
        setProducts((prev) => [
          ...prev,
          DeletedProducts.filter(
            (Product) => Product.PRODUCT_ID === PRODUCT_ID
          )[0],
        ]);
        setDeletedProducts((prev) =>
          prev.filter((Product) => Product.PRODUCT_ID !== PRODUCT_ID)
        );
      } else {
      }
    });
  };

  const handleOpenAddingModal = () => {
    setIsAdding(true);
  };

  const handleCloseAddingModal = () => {
    setIsAdding(false);
  };

  const handleOpenModal = (PRODUCT_ID) => {
    setSelectedProductID(PRODUCT_ID);
    setProductModels((prev) => {
      const VARIANTS = Products.filter(
        (Product) => Product.PRODUCT_ID === PRODUCT_ID
      )[0].VARIANTS;
      return VARIANTS ? VARIANTS : [];
    });
    setModal(true);
  };

  const handleCloseModal = () => {
    setSelectedImg({});
    setSelectedProductID("");
    setIsEditing(false);
    setModal(false);
  };

  const handleOpenPopup = (type, VARIANT_ID) => {
    setPopup({
      isOpen: true,
      type: type,
    });
    VARIANT_ID && setSelectedModelID(VARIANT_ID);
  };

  const handleClosePopup = () => {
    setPopup(() => ({
      isOpen: false,
      type: "",
    }));
    setSelectedModelID(null);
  };

  const handleOpenStorage = () => {
    setStorage(true);
  };

  const handleCloseStorage = () => {
    setStorage(false);
  };

  const handleAddNewModel = (data) => {
    data.OPTIONS = [];
    setProductModels((prev) => [...prev, data]);
  };

  const handleDeleteModel = (VARIANT_ID) => {
    const text = `Xo sn phm ${VARIANT_ID} ?`;
    if (window.confirm(text) == true) {
      setProductModels(
        ProductModels.filter((VARIANT) => VARIANT.VARIANT_ID !== VARIANT_ID)
      );
    } else {
      alert(" hu xo")
    }
  };

  const handleAddNewOption = (data) => {
    setProductModels((prevModels) => {
      prevModels.map((Model) => {
        if (Model.VARIANT_ID === SelectedModelID) {
          Model.OPTIONS.push(data);
        }
        return Model;
      });
      return prevModels;
    });
  };

  const handleDeleteOption = (VARIANT_ID, OPTION_ID) => {
    setProductModels((prevModels) => {
      return prevModels.map((Model) => {
        if (Model.VARIANT_ID === VARIANT_ID) {
          const newOPTIONS = Model.OPTIONS.filter(
            (OPTION) => OPTION.OPTION_ID !== OPTION_ID
          );
          return { ...Model, OPTIONS: newOPTIONS };
        }
        return Model;
      });
    });
  };

  const handleEditOn = (event) => {
    event.preventDefault();
    setIsEditing(true);
  };

  return (
    <>
      <div className={styles.section_container}>
        <div className={styles.product_adding_pane}>
          <button
            className={`btn btn--primary`}
            onClick={() => handleOpenAddingModal()}
          >
            Thm sn phm
          </button>
          <button
            className={`btn ${styles.adding_pane_bin}`}
            onClick={() => handleOpenStorage()}
          >
            Sn phm  n
          </button>
        </div>
        <table className={styles.products_table}>
          <thead>
            <tr>
              <th>#</th>
              <th>Tn sn phm</th>
              <th>nh</th>
              <th>M t</th>
              <th>Gi</th>
              <th className="small_flex_column">S lng</th>
              <th className="small_flex_column">n v</th>
              <th>Thm ngy</th>
              <th>Nh sn xut</th>
              <th style={{ flex: "0.2 1" }}></th>
            </tr>
          </thead>
          <tbody>
            {Products.map((Product) => (
              <tr key={Product.PRODUCT_ID}>
                <td>{Product.PRODUCT_ID}</td>
                <td className="text-l">
                  <span className={styles.text}>{Product.PRODUCT_NAME}</span>
                </td>
                <td>
                  <div
                    className={styles.img_wrapper}
                    style={{
                      backgroundImage: `url(${Product.PRODUCT_IMAGE})`,
                      backgroundSize: "contain",
                      backgroundRepeat: "no-repeat",
                      backgroundPosition: "contain",
                    }}
                  >
                    {/* <img
                      src={
                        (SelectedImg &&
                          SelectedProductID === Product.PRODUCT_ID &&
                          SelectedImg.name) ||
                        Product.PRODUCT_IMAGE
                      }
                      alt="Product Image"
                      className={styles.product_img}
                    /> */}
                  </div>
                </td>
                <td className="text-l">
                  <span className={styles.text}>
                    {Product.PRODUCT_DESCRIPTION}
                  </span>
                </td>
                <td>
                  {Number.parseFloat(Product.PRODUCT_PRICE).toLocaleString()}
                </td>
                <td className="small_flex_column">{Product.STOCK}</td>
                <td className="small_flex_column">{Product.PRODUCT_UNIT}</td>
                <td>{Product.PRODUCT_CREATE_AT}</td>
                <td>{Product.MANUFACTURER}</td>
                <td
                  style={{ flex: "0.2 1" }}
                  className={styles.product_control}
                >
                  <i
                    className={`fa-solid fa-ellipsis-vertical ${styles.product_control_icon}`}
                    onClick={(e) => openOptionsMenu(e)}
                  />
                  <div className={styles.options_container}>
                    <div
                      className={`${styles.option} ${styles.edit}`}
                      onClick={() => handleOpenModal(Product.PRODUCT_ID)}
                    >
                      Xem thm
                    </div>
                    <div
                      className={`${styles.option} ${styles.delete}`}
                      onClick={() => DeleteProduct(Product.PRODUCT_ID)}
                    >
                      n
                    </div>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {Modal &&
        Products.map(
          (Product) =>
            Product.PRODUCT_ID === SelectedProductID && (
              <div className={styles.modal} key={Product.PRODUCT_ID}>
                <form onSubmit={handleSubmit(UpdateProduct)}>
                  <div className={styles.modal_container}>
                    <div className={styles.modal_header}>
                      <div className={styles.hero_title}>Qun l sn phm</div>
                    </div>
                    <div className={styles.modal_body}>
                      <div className={styles.content_container}>
                        <div className="grid wide">
                          <div className="row">
                            <div className="col c-6">
                              <div className={styles.input_group}>
                                <div className={styles.input_area}>
                                  <div className={styles.input_wrapper}>
                                    <span className={styles.input}>
                                      {Product.PRODUCT_ID}
                                    </span>
                                  </div>
                                  <label
                                    htmlFor="PRODUCT_ID"
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    M sn phm
                                  </label>
                                </div>
                                <div className={styles.input_area}>
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <input
                                        type="text"
                                        className={styles.input}
                                        defaultValue={Product.PRODUCT_NAME}
                                        {...register("PRODUCT_NAME")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.PRODUCT_NAME}
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    Tn sn phm
                                  </label>
                                </div>
                                <div className={styles.input_area}>
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <input
                                        type="text"
                                        className={styles.input}
                                        defaultValue={Product.PRODUCT_PRICE}
                                        {...register("PRODUCT_PRICE")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.PRODUCT_PRICE}
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    Gi
                                  </label>
                                </div>
                              </div>

                              <div
                                className={`${styles.input_group} ${styles.sub_group}`}
                              >
                                <div
                                  className={`${styles.input_area} ${styles.sub_item}`}
                                >
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <input
                                        type="number"
                                        className={styles.input}
                                        defaultValue={Product.STOCK}
                                        {...register("STOCK")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.STOCK}
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    S lng
                                  </label>
                                </div>
                                <div
                                  className={`${styles.input_area} ${styles.sub_item}`}
                                >
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <select
                                        className={styles.input}
                                        {...register("CATEGORY")}
                                        defaultValue={Product.CATEGORY}
                                      >
                                        {SubItem.Categories.map((CATEGORY) => (
                                          <option
                                            key={CATEGORY.CATEGORY_ID}
                                            value={CATEGORY.CATEGORY_ID}
                                          >
                                            {CATEGORY.CATEGORY_NAME}
                                          </option>
                                        ))}
                                      </select>
                                    ) : (
                                      <div className={styles.input}>
                                        {
                                          SubItem.Categories.filter(
                                            (CATEGORY) =>
                                              CATEGORY.CATEGORY_ID ===
                                              Product.CATEGORY
                                          )[0].CATEGORY_NAME
                                        }
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    Danh mc
                                  </label>
                                </div>
                              </div>

                              <div
                                className={`${styles.input_group} ${styles.sub_group}`}
                              >
                                <div
                                  className={`${styles.input_area} ${styles.sub_item}`}
                                >
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <input
                                        type="text"
                                        className={styles.input}
                                        defaultValue={Product.PRODUCT_UNIT}
                                        {...register("PRODUCT_UNIT")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.PRODUCT_UNIT}
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    n v
                                  </label>
                                </div>
                                <div
                                  className={`${styles.input_area} ${styles.sub_item}`}
                                >
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <select
                                        className={styles.input}
                                        {...register("MANUFACTURER")}
                                        defaultValue={Product.MANUFACTURER}
                                      >
                                        {SubItem.Manufacturer.map(
                                          (MANUFACTURER) => (
                                            <option
                                              key={MANUFACTURER.MANUFACTURER_ID}
                                              value={
                                                MANUFACTURER.MANUFACTURER_ID
                                              }
                                            >
                                              {MANUFACTURER.MANUFACTURER_NAME}
                                            </option>
                                          )
                                        )}
                                      </select>
                                    ) : (
                                      <div className={styles.input}>
                                        {
                                          SubItem.Manufacturer.filter(
                                            (MANUFACTURER) =>
                                              MANUFACTURER.MANUFACTURER_ID ===
                                              Product.MANUFACTURER
                                          )[0].MANUFACTURER_NAME
                                        }
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    Nh sn xut
                                  </label>
                                </div>
                              </div>
                              <div
                                className={`${styles.input_group} ${styles.sub_group}`}
                              >
                                <div
                                  className={`${styles.input_area} ${styles.sub_item}`}
                                  style={{
                                    flexBasis: "calc(50% - 15px)",
                                    flexGrow: "0",
                                    marginRight: "auto",
                                  }}
                                >
                                  <div className={styles.input_wrapper}>
                                    {IsEditing ? (
                                      <input
                                        type="text"
                                        className={styles.input}
                                        defaultValue={Product.DISCOUNT}
                                        {...register("DISCOUNT")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.DISCOUNT}
                                      </div>
                                    )}
                                  </div>
                                  <label
                                    htmlFor=""
                                    className={`${styles.label} ${styles.focus}`}
                                  >
                                    Gim gi
                                  </label>
                                </div>
                              </div>
                            </div>
                            <div className="col c-6">
                              <div className={styles.input_group}>
                                <div className={styles.image_preview}>
                                  <div
                                    className={styles.image}
                                    style={{
                                      backgroundImage: `url('${
                                        (SelectedImg && SelectedImg.name) ||
                                        Product.PRODUCT_IMAGE
                                      }')`,
                                    }}
                                  ></div>
                                </div>
                                {IsEditing && (
                                  <div className={styles.input_file_area}>
                                    <input
                                      type="file"
                                      className={styles.input_file}
                                      onChange={(e) =>
                                        setSelectedImg({
                                          name: URL.createObjectURL(
                                            e.target.files[0]
                                          ),
                                          file: e.target.files[0],
                                        })
                                      }
                                    />
                                    <div className={styles.input_file_box}>
                                      <span>Chn nh</span>
                                    </div>
                                  </div>
                                )}
                              </div>
                            </div>
                            <div className="col c-12">
                              <div className={styles.input_group}>
                                <table className={styles.models_table}>
                                  <caption>Phn loi</caption>
                                  <thead>
                                    <tr>
                                      <th>Tn</th>
                                      <th>Ni dung</th>
                                      {IsEditing && <th>n</th>}
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {ProductModels &&
                                      ProductModels.map((VARIANT) => (
                                        <tr key={VARIANT.VARIANT_ID}>
                                          <td>
                                            <span
                                              className={styles.VARIANT_NAME}
                                            >
                                              {VARIANT.VARIANT_NAME} ({" "}
                                              {VARIANT.VARIANT_ID} )
                                            </span>
                                          </td>
                                          <td>
                                            {VARIANT.OPTIONS.map((OPTION) => (
                                              <div
                                                className={styles.model_item}
                                                key={OPTION.OPTION_ID}
                                              >
                                                {OPTION.OPTION_NAME} ({" "}
                                                {OPTION.OPTION_ID} )
                                                {IsEditing && (
                                                  <div
                                                    className={
                                                      styles.model_controller
                                                    }
                                                    onClick={() =>
                                                      handleDeleteOption(
                                                        VARIANT.VARIANT_ID,
                                                        OPTION.OPTION_ID
                                                      )
                                                    }
                                                  >
                                                    &times;
                                                  </div>
                                                )}
                                              </div>
                                            ))}
                                            {IsEditing && (
                                              <div
                                                className={
                                                  styles.add_model_item
                                                }
                                                onClick={() =>
                                                  handleOpenPopup(
                                                    "VARIANT_OPTION",
                                                    VARIANT.VARIANT_ID
                                                  )
                                                }
                                              >
                                                &#x2B; Thm
                                              </div>
                                            )}
                                          </td>
                                          {IsEditing && (
                                            <td>
                                              <span
                                                className={styles.model_action}
                                                onClick={() =>
                                                  handleDeleteModel(
                                                    VARIANT.VARIANT_ID
                                                  )
                                                }
                                              >
                                                n
                                              </span>
                                            </td>
                                          )}
                                        </tr>
                                      ))}
                                    {IsEditing ? (
                                      <tr>
                                        <td colSpan="3">
                                          <div className={styles.add_model}>
                                            <button
                                              type="button"
                                              className="btn"
                                              onClick={() =>
                                                handleOpenPopup("VARIANT")
                                              }
                                            >
                                              &#x2B; Thm phn loi
                                            </button>
                                          </div>
                                        </td>
                                      </tr>
                                    ) : Product.VARIANTS ? null : (
                                      <tr>
                                        <td colSpan="3">
                                          Khng c phn loi no
                                        </td>
                                      </tr>
                                    )}
                                  </tbody>
                                </table>
                              </div>
                            </div>
                            <div className="col c-12">
                              <div className={styles.description_section}>
                                <div className={styles.description_container}>
                                  <div className={styles.title}>
                                    M t sn phm
                                  </div>
                                  <div className={styles.content_container}>
                                    {IsEditing ? (
                                      <textarea
                                        type="text"
                                        className={styles.input}
                                        defaultValue={
                                          Product.PRODUCT_DESCRIPTION
                                        }
                                        {...register("PRODUCT_DESCRIPTION")}
                                      />
                                    ) : (
                                      <div className={styles.input}>
                                        {Product.PRODUCT_DESCRIPTION}
                                      </div>
                                    )}
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className={styles.modal_controllers}>
                      <button
                        className="btn"
                        type="button"
                        onClick={() => handleCloseModal()}
                      >
                        Tr v
                      </button>
                      {IsEditing ? (
                        <button className="btn btn--primary">Lu</button>
                      ) : (
                        <button
                          type="button"
                          className="btn btn--primary"
                          onClick={(e) => handleEditOn(e)}
                        >
                          Chnh sa
                        </button>
                      )}
                    </div>
                  </div>
                </form>
              </div>
            )
        )}

      {IsAdding && (
        <div className={styles.modal}>
          <form onSubmit={handleSubmit3(UploadProduct)}>
            <div className={styles.modal_container}>
              <div className={styles.modal_header}>
                <div className={styles.hero_title}>Thm sn phm mi</div>
              </div>
              <div className={styles.modal_body}>
                <div className={styles.content_container}>
                  <div className="grid wide">
                    <div className="row">
                      <div className="col c-6">
                        <div className={styles.input_group}>
                          <div className={styles.input_area}>
                            <div className={styles.input_wrapper}>
                              <input
                                type="text"
                                className={styles.input}
                                {...register3("PRODUCT_ID")}
                              />
                            </div>
                            <label
                              htmlFor="PRODUCT_ID"
                              className={`${styles.label} ${styles.focus}`}
                            >
                              M sn phm
                            </label>
                          </div>
                          <div className={styles.input_area}>
                            <div className={styles.input_wrapper}>
                              <input
                                type="text"
                                className={styles.input}
                                {...register3("PRODUCT_NAME")}
                              />
                            </div>
                            <label
                              htmlFor=""
                              className={`${styles.label} ${styles.focus}`}
                            >
                              Tn sn phm
                            </label>
                          </div>
                          <div className={styles.input_area}>
                            <div className={styles.input_wrapper}>
                              <input
                                type="text"
                                className={styles.input}
                                {...register3("PRODUCT_PRICE")}
                              />
                            </div>
                            <label
                              htmlFor=""
                              className={`${styles.label} ${styles.focus}`}
                            >
                              Gi
                            </label>
                          </div>
                        </div>

                        <div
                          className={`${styles.input_group} ${styles.sub_group}`}
                        >
                          <div
                            className={`${styles.input_area} ${styles.sub_item}`}
                          >
                            <div className={styles.input_wrapper}>
                              <input
                                type="number"
                                className={styles.input}
                                {...register3("STOCK")}
                              />
                            </div>
                            <label
                              htmlFor=""
                              className={`${styles.label} ${styles.focus}`}
                            >
                              S lng
                            </label>
                          </div>
                          <div
                            className={`${styles.input_area} ${styles.sub_item}`}
                          >
                            <div className={styles.input_wrapper}>
                              <select
                                className={styles.input}
                                {...register3("CATEGORY")}
                              >
                                {SubItem.Categories.map((CATEGORY) => (
                                  <option
                                    key={CATEGORY.CATEGORY_ID}
                                    value={CATEGORY.CATEGORY_ID}
                                  >
                                    {CATEGORY.CATEGORY_NAME}
                                  </option>
                                ))}
                              </select>
                            </div>
                            <label
                              htmlFor=""
                              className={`${styles.label} ${styles.focus}`}
                            >
                              Danh mc
                            </label>
                          </div>
                        </div>

                        <div
                          className={`${styles.input_group} ${styles.sub_group}`}
                        >
                          <div
                            className={`${styles.input_area} ${styles.sub_item}`}
                          >
                            <div className={styles.input_wrapper}>
                              <input
                                type="text"
                                className={styles.input}
                                {...register3("PRODUCT_UNIT")}
                              />
                            </div>
                            <label
                              htmlFor=""
                              className={`${styles.label} ${styles.focus}`}
                            >
                              n v
                            </label>
                          </div>
                          <div
                            className={`${styles.input_area} ${styles.sub_item}`}
                          >
                            <div className={styles.input_wrapper}>
                              <select
                                className={styles.input}
                                {...register3("MANUFACTURER")}
                              >
                                {SubItem.Manufacturer.map((MANUFACTURER) => (
                                  <option
                                    key={MANUFACTURER.MANUFACTURER_ID}
                                    value={MANUFACTURER.MANUFACTURER_ID}
                                  >
                                    {MANUFACTURER.MANUFACTURER_NAME}
                                  </option>
                                ))}
                              </select>
                            </div>
                            <label
                              className={`${styles.label} ${styles.focus}`}
                            >
                              Nh sn xut
                            </label>
                          </div>
                        </div>
                      </div>
                      <div className="col c-6">
                        <div className={styles.input_group}>
                          <div className={styles.image_preview}>
                            <div
                              className={styles.image}
                              style={{
                                backgroundImage: `url('${
                                  SelectedImg && SelectedImg.name
                                }')`,
                              }}
                            ></div>
                          </div>

                          <div className={styles.input_file_area}>
                            <input
                              type="file"
                              className={styles.input_file}
                              onChange={(e) =>
                                setSelectedImg({
                                  name: URL.createObjectURL(e.target.files[0]),
                                  file: e.target.files[0],
                                })
                              }
                            />
                            <div className={styles.input_file_box}>
                              <span>Chn nh</span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col c-12">
                        <div className={styles.input_group}>
                          <table className={styles.models_table}>
                            <caption>Phn loi</caption>
                            <thead>
                              <tr>
                                <th>Tn</th>
                                <th>Ni dung</th>
                                {IsEditing && <th>n</th>}
                              </tr>
                            </thead>
                            <tbody>
                              {/* {ProductModels &&
                                ProductModels.map((VARIANT) => (
                                  <tr key={VARIANT.VARIANT_ID}>
                                    <td>
                                      <span className={styles.VARIANT_NAME}>
                                        {VARIANT.VARIANT_NAME} ({" "}
                                        {VARIANT.VARIANT_ID} )
                                      </span>
                                    </td>
                                    <td>
                                      {VARIANT.OPTIONS.map((OPTION) => (
                                        <div
                                          className={styles.model_item}
                                          key={OPTION.OPTION_ID}
                                        >
                                          {OPTION.OPTION_NAME} ({" "}
                                          {OPTION.OPTION_ID} )
                                          {IsEditing && (
                                            <div
                                              className={
                                                styles.model_controller
                                              }
                                              onClick={() =>
                                                handleDeleteOption(
                                                  VARIANT.VARIANT_ID,
                                                  OPTION.OPTION_ID
                                                )
                                              }
                                            >
                                              &times;
                                            </div>
                                          )}
                                        </div>
                                      ))}
                                      {IsEditing && (
                                        <div
                                          className={styles.add_model_item}
                                          onClick={() =>
                                            handleOpenPopup(
                                              "VARIANT_OPTION",
                                              VARIANT.VARIANT_ID
                                            )
                                          }
                                        >
                                          &#x2B; Thm
                                        </div>
                                      )}
                                    </td>
                                    {IsEditing && (
                                      <td>
                                        <span
                                          className={styles.model_action}
                                          onClick={() =>
                                            handleDeleteModel(
                                              VARIANT.VARIANT_ID
                                            )
                                          }
                                        >
                                          n
                                        </span>
                                      </td>
                                    )}
                                  </tr>
                                ))} */}
                              <tr>
                                <td colSpan="3">
                                  <div className={styles.add_model}>
                                    <button
                                      type="button"
                                      className="btn"
                                      onClick={() => handleOpenPopup("VARIANT")}
                                    >
                                      &#x2B; Thm phn loi
                                    </button>
                                  </div>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      <div className="col c-12">
                        <div className={styles.description_section}>
                          <div className={styles.description_container}>
                            <div className={styles.title}>M t sn phm</div>
                            <div className={styles.content_container}>
                              <textarea
                                type="text"
                                className={styles.input}
                                {...register3("PRODUCT_DESCRIPTION")}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className={styles.modal_controllers}>
                <button
                  className="btn"
                  type="button"
                  onClick={() => handleCloseAddingModal()}
                >
                  Tr v
                </button>

                <button className="btn btn--primary">Thm</button>
              </div>
            </div>
          </form>
        </div>
      )}

      {Popup.isOpen && (
        <div className={styles.popup}>
          <div className={styles.popup_container}>
            <form
              onSubmit={handleSubmit2(
                Popup.type === "VARIANT"
                  ? handleAddNewModel
                  : handleAddNewOption
              )}
            >
              <div className={styles.title}>
                {Popup.type === "VARIANT" ? "Thm phn loi" : "Thm la chn"}
              </div>
              <div className={styles.content}>
                <div className={styles.area}>
                  <div className={styles.product_VARIANT_NAME}>
                    {Popup.type === "VARIANT" ? "M phn loi" : "M la chn"}
                  </div>
                  <input
                    type="text"
                    className={styles.product_model_input}
                    {...register2(
                      Popup.type === "VARIANT" ? "VARIANT_ID" : "OPTION_ID"
                    )}
                  />
                </div>
                <div className={styles.area}>
                  <div className={styles.product_VARIANT_NAME}>
                    {Popup.type === "VARIANT"
                      ? "Tn phn loi"
                      : "Tn la chn"}
                  </div>
                  <input
                    type="text"
                    className={styles.product_model_input}
                    {...register2(
                      Popup.type === "VARIANT" ? "VARIANT_NAME" : "OPTION_NAME"
                    )}
                  />
                </div>
              </div>
              <div className={styles.product_model_controllers}>
                <button
                  type="button"
                  className="btn"
                  onClick={() => handleClosePopup()}
                >
                  Tr v
                </button>
                <button className="btn btn--primary">Thm</button>
              </div>
            </form>
          </div>
        </div>
      )}

      {Storage && (
        <DeletedStorage
          Products={DeletedProducts}
          CancelDeleteProduct={CancelDeleteProduct}
          handleCloseStorage={handleCloseStorage}
        />
      )}
    </>
  );
}

export default AdminShop;
