export async function loginUser(dispatch, loginPayload) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(loginPayload),
      };

    try {
        dispatch({ type: "REQUEST_LOGIN"})
        let response = await fetch("http://localhost/server/login.php?login", requestOptions)
        let data = await response.json();

        if(data.CONNECTED) {
            dispatch({type: "LOGIN_SUCCESS", payload: data})
            localStorage.setItem("currentUser", JSON.stringify(data))
            return data;
        }

        dispatch({type: "LOGIN_ERROR", error: data.MESSAGE})
        return data;
    } catch(error) {
        dispatch({type: "LOGIN_ERROR", error: error})
    }
}

export function logout(dispatch) {
    dispatch({type: "LOGOUT"})
    localStorage.removeItem('currentUser');
}

export function addToCart(dispatch, data) {
    dispatch({type: "ADD_TO_CART", payload: data})
} 

export function removeFromCart(dispatch, data) {
    dispatch({type: "REMOVE_FROM_CART", payload: data})
}

export function clearCart(dispatch) {
    dispatch({type: "CLEAR_CART"})
}