import React from 'react'
import { Link } from 'react-router-dom'

import Slider from "react-slick";
import styles from './dyslider.module.css';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const sliderImgs = [
    "/resources/img/slider/6024960.jpg",
    "/resources/img/slider/slider_img_2.jpg",
    "/resources/img/slider/slider_img_3.jpg",
    "/resources/img/slider/slider_img_4.png",
    "/resources/img/slider/slider_img_5.jpg"
]

function HomeSlider({ settings, data, reference, ...props }) {

    return (
        <div>
            <Slider {...settings}>
                {data && data.map((item, index) => (
                    <Link to={`${reference}/${item.PRODUCT_ID}` || ''} className="slick-item-container" key={index}>
                        <div
                            className={styles.item_link}
                            style={{
                                backgroundImage: `url('${item.PRODUCT_IMAGE}')`,
                                backgroundSize: "contain",
                                backgroundRepeat: "no-repeat",
                            }} />
                        <div className={styles.item_name_wrapper} >
                            <span className={styles.item_name}>{item.PRODUCT_NAME || ''}</span>
                        </div>
                    </Link>
                ))}
            </Slider>
        </div>
    )
}

export default HomeSlider