import { useState, useImperativeHandle, forwardRef, useRef, useEffect } from "react";
import { useForm } from "react-hook-form";
import styles from "./onlinepayment.module.css";
import { useCookies } from "react-cookie";

function OnlinePaymentForm({ setIsShown, setCreditCard }) {
  const [cookies, setCookie] = useCookies("creditcard");

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    reset({
      keepValues: false
    })
  }, [])

  const onSubmit = data => {
    setCreditCard(data)
    setCookie("creditcard", data, {path: "/"})
    setIsShown(false)
  }

  const handleFocus = (e) => {
    const formWrapper = e.target.closest(`.${styles.form_wrapper}`);
    const label = formWrapper.querySelector(`.${styles.label}`);

    label.classList.add(`${styles.focus}`);
  };

  const handleBlur = (e) => {
    if (!e.target.value) {
      const formWrapper = e.target.closest(`.${styles.form_wrapper}`);
      const label = formWrapper.querySelector(`.${styles.label}`);

      label.classList.remove(`${styles.focus}`);
    }
    console.log(e.target.value);
  };

  return (
    <div
      className={`${styles.form_overlay} ${styles.display}`}
    >
      <div
        className={`${styles.container}`}
        onClick={(e) => e.stopPropagation()}
      >
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className={styles.title}>Thm th</div>
          <div className={styles.informations}>
            <div className={styles.title}>Chi tit th</div>
            <div className={styles.form_wrapper}>
              <div className={styles.input_wrapper}>
                <input
                  type="text"
                  onFocus={(e) => handleFocus(e)}
                  onBlur={(e) => handleBlur(e)}
                  {...register("cardnumber", {
                    required: "khng c b trng",
                    pattern: {
                      value: /^4[0-9]{12}(?:[0-9]{3})?$/,
                      message: "khng hp l",
                    },
                  })}
                />
              </div>
              <label htmlFor="cardnumber" className={styles.label}>
                S th
              </label>
            </div>
            <div className={styles.input_group}>
              <div className={`${styles.form_wrapper} ${styles.input_3}`}>
                <div className={styles.input_wrapper}>
                  <input
                    type="month"
                    onFocus={(e) => handleFocus(e)}
                    onBlur={(e) => handleBlur(e)}
                    {...register("expiredate", {
                      required: {
                        value: true,
                        message: "ngy ht hn khng c b trng",
                      },
                    })}
                  />
                </div>
                <label htmlFor="cardnumber" className={styles.label}>
                  Ngy ht hn (MM/YY)
                </label>
              </div>
              <div className={`${styles.form_wrapper} ${styles.input_1}`}>
                <div className={styles.input_wrapper}>
                  <input
                    type="text"
                    onFocus={(e) => handleFocus(e)}
                    onBlur={(e) => handleBlur(e)}
                    {...register("cvv", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      pattern: {
                        value: /^[0-9]{3,4}$/,
                        message: "khng hp l",
                      },
                    })}
                  />
                </div>
                <label htmlFor="cardnumber" className={styles.label}>
                  M CVV
                </label>
              </div>
            </div>
            <div className={styles.form_wrapper}>
              <div className={styles.input_wrapper}>
                <input
                  type="text"
                  onFocus={(e) => handleFocus(e)}
                  onBlur={(e) => handleBlur(e)}
                  {...register("cardowner", {
                    required: {
                      value: true,
                      message: "khng c b trng",
                    },
                    minLength: {
                      value: 8,
                      message: "khng hp l",
                    },
                  })}
                />
              </div>
              <label htmlFor="cardnumber" className={styles.label}>
                H v tn ch th
              </label>
            </div>
          </div>
          {Object.keys(errors).length !== 0 && (
            <>
              <div className={styles.error_title}>Li</div>
              <ul className={styles.error_container}>
                {errors.cardnumber && <li>S th: {errors.cardnumber.message}</li>}
                {errors.expiredate && <li>Ngy ht hn: {errors.expiredate.message}</li>}
                {errors.cvv && <li>M CVV: {errors.cvv.message}</li>}
                {errors.cardowner && <li>H v tn ch th: {errors.cardowner.message}</li>}
              </ul>
            </>
          )}
          <div className={styles.control_container}>
            <button
              type="button"
              className={`btn ${styles.sub_button}`}
              onClick={() => setIsShown(false)}
            >
              Tr li
            </button>
            <button className={`btn btn--primary ${styles.primary_button}`}>
              Xc nhn
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default OnlinePaymentForm;
