import React, { memo } from 'react'

import styles from './ItemStorage.module.css'

function ItemStorage({ Reference, Items, handleCloseStorage, CancelDeleteItem }) {

  return (
    <div className={styles.modal}>
        <div className={styles.modal_container}>
            <div className={styles.modal_header}>
                <div className={styles.hero_title}>{Reference==="MANUFACTURER"?"Nh phn phi  n":"Danh mc  n"}</div>
            </div> 
            <div className={styles.modal_body}>
                <table className={styles.table}>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>{Reference==="MANUFACTURER"?"Tn nh phn phi":"Tn danh mc"}</th>
                            <th>Tu chn</th>
                        </tr>
                    </thead>
                    <tbody>
                        {Items.map(Item => (
                            <tr key={Reference==="MANUFACTURER"?Item.MANUFACTURER_ID:Item.CATEGORY_ID}>
                                <td>{Reference==="MANUFACTURER"?Item.MANUFACTURER_ID:Item.CATEGORY_ID}</td>
                                <td>{Reference==="MANUFACTURER"?Item.MANUFACTURER_NAME:Item.CATEGORY_NAME}</td>
                                <td>
                                    <div className={styles.category_controller} onClick={() => CancelDeleteItem(Reference==="MANUFACTURER"?Item.MANUFACTURER_ID:Item.CATEGORY_ID)}>Hu</div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <div className={styles.modal_controller}>
                <button className="btn" onClick={() => handleCloseStorage()}>Tr v</button>
            </div>
        </div>
    </div>
  )
}

export default memo(ItemStorage)