import { useState, useEffect } from 'react'
import { Link, useParams, useNavigate, Navigate, useLocation } from 'react-router-dom'
import axios from 'axios'
import Loading from '../../components/Loading'
import styles from './productviewer.module.css'
import MoneyShow from '../../components/MoneyShow/MoneyShow'
import { useCookies } from 'react-cookie'
import { addToCart, useCartState } from '../../context'

axios.defaults.withCredentials = true;

const RatingStarRendering = (quantity) => {
    const StarContainer = []
    const charge = Math.round(quantity - Number.parseInt(quantity))
    for (let i = 0; i < Math.floor(quantity); i++)
        StarContainer.push(<i key={i} className={`fa-solid fa-star ${styles.item_rating_icon__active}`}></i>)
    if (charge)
        StarContainer.push(<i key={charge + Math.floor(quantity)} className={`fa-solid fa-star-half-stroke ${styles.item_rating_icon__active}`}></i>)
    for (let i = 5; i > (Math.floor(quantity) + charge); i--)
        StarContainer.push(<i key={i} className={`fa-regular fa-star ${styles.item_rating_icon}`}></i>)

    return StarContainer
}

function ProductViewer() {
    const [product, setProduct] = useState({})
    const [isLoading, setIsLoading] = useState(true)
    const [quantity, setQuantity] = useState(1)
    const [On, setOn] = useState(false)
    const [cookies, setCookies] = useCookies("cart")
    const { cart, dispatch } = useCartState()
    const location = useLocation()
    const params = useParams()

    const handleAddToCart = () => {
        axios({
            url: "http://localhost/server/login-authenic.php",
            method: "GET"
        })
        .then(response => {
            const result = response.data;
            if(result.SUCCESS) {
                if(cart.find(item => item.PRODUCT_ID === params.productID) || cookies?.cart?.find(item => item.PRODUCT_ID === params.productID)) {
                    alert(" c sn phm ny trong gi")
                } else {
                    alert("Thm sn phm thnh cng")
                    const newCart = cookies.cart?[...cookies.cart, {PRODUCT_ID: params.productID, QUANTITY: quantity}]:[{PRODUCT_ID: params.productID, QUANTITY: quantity}];
                    setCookies("cart", newCart, {path: "/"})
                    addToCart(dispatch, {PRODUCT_ID: params.productID, QUANTITY: quantity})
                }
            } else {
                setOn(true)
            }
        })
    }

    useEffect(() => {
        setIsLoading(true)
        axios(`http://localhost/server/product.php?PRODUCT_ID=${params.productID}`)
            .then(res => {
                setIsLoading(false)
                setProduct(res.data.PRODUCTS[0])
            })
    }, [])

    

    const QuantityControl = (action) => {
        const quantity = document.getElementById("product_quantity")

        if (action === 'minus')
            quantity.value = Number.parseInt(quantity.value) - 1
        else
            quantity.value = Number.parseInt(quantity.value) + 1
    }

    return (
        On?<Navigate  to="/login" state={{from: location}} replace/>
        :<div className={styles.container}>
        {isLoading ? <Loading /> :
            <div className="grid wide">
                <div className="row">
                    <div className="col c-12">
                        <div className={styles.ProductViewer}>
                            <div className={styles.img_wrapper}>
                                <div style={{ backgroundImage: 'url("' + product.PRODUCT_IMAGE + '")' }} className={styles.img} />
                            </div>
                            <div className={styles.info_container}>
                                <div className={styles.title}>{product.PRODUCT_NAME}</div>
                                <div className={styles.rating_wrapper}>
                                    <div className={styles.rating_star}>
                                        <span className={styles.number}>{product.RATING}</span>
                                        <div className={styles.rating_star_wrapper}>
                                            {RatingStarRendering(product.RATING)}
                                        </div>
                                    </div>
                                </div>
                                <div className={styles.price_wrapper}>
                                    {
                                        product.DISCOUNT !== '0'?
                                        <>
                                        <div className={styles.price_base}>{MoneyShow({price: product.PRODUCT_PRICE})}</div>
                                    <div className={styles.price_new}>{MoneyShow({price: product.PRODUCT_PRICE, discount: product.DISCOUNT})}</div>
                                    <div className={styles.price_discount_badge}>{product.DISCOUNT}% Gim</div>
                                    </>:
                                    <div className={styles.price_new}>{MoneyShow({price: product.PRODUCT_PRICE})}</div>
                                    }
                                </div>
                                <div className={styles.product_info_container}>
                                    <div className={styles.product_info_item}>
                                        <div className={styles.label}>Danh mc</div>
                                        <div className={styles.category_container}>
                                            <Link to="/">{product.CATEGORY}</Link>
                                        </div>
                                    </div>
                                    <div className={styles.product_info_item}>
                                        <div className={styles.label}>S lng</div>
                                        <div className="quantity_control">
                                            <button className="quantity_control_button" onClick={() => QuantityControl('minus')}>-</button>
                                            <input type='number' className="quantity_control_input" id="product_quantity" value={quantity} onChange={e => setQuantity(e.target.value)} />
                                            <button className="quantity_control_button" onClick={() => QuantityControl('plus')}>+</button>
                                        </div>
                                    </div>
                                </div>
                                <div className={styles.product_control_container}>
                                    <button className={styles.product_control__addtocart_button} onClick={() => handleAddToCart()}>
                                        <i className="fa-solid fa-cart-plus" />
                                        Thm vo gi hng
                                    </button>
                                    <button className={styles.product_control__buynow_button}>
                                        Mua ngay
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div className={styles.detail_container}>
                            <div className={styles.detail}>
                                <div className={styles.hero_tittle}>Chi tit sn phm</div>
                                <div className={styles.description_container}>
                                    <div className={styles.detail_item}>
                                        <span className={styles.title}>Danh mc</span>
                                        <span className={styles.description}>
                                            <Link to={`/${product.id}`}>{product.CATEGORY}</Link>
                                        </span>
                                    </div>
                                    <div className={styles.detail_item}>
                                        <span className={styles.title}>Thng hiu</span>
                                        <span className={styles.description}>
                                            {product.MANUFACTURER}
                                        </span>
                                    </div>
                                    <div className={styles.detail_item}>
                                        <span className={styles.title}>Kho hng</span>
                                        <span className={styles.description}>
                                            {product.STOCK}
                                        </span>
                                    </div>
                                </div>
                            </div>
                            <div className={styles.detail}>
                                <div className={styles.hero_tittle}>M t sn phm</div>
                                <div className={styles.description_container}>
                                    {product.PRODUCT_DESCRIPTION}
                                </div>
                            </div>
                        </div>

                        <div className={styles.rating_container}>
                            <div className={styles.hero_tittle}>nh gi sn phm</div>
                        </div>
                    </div>
                </div>
            </div>
        }
    </div>
    )
}

export default ProductViewer