import { memo } from "react";
import styles from "./pagination.module.css";

function Pagination({
  productsPerPage,
  totalProductsLength,
  handleChangeCurrentFilter,
  currentPage,
  isLoading,
}) {
  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalProductsLength / productsPerPage); i++) {
    pageNumbers.push(i);
  }

  const handleChangePage = (page) => {
    page = page === -1 ? currentPage - 1 : page === 0 ? currentPage + 1 : page;
    handleChangeCurrentFilter("page", page)
  }

  return (
    <>
      {isLoading || (
        <div className={styles.container}>
          <ul>
            {currentPage !== 1 ? (
              <li>
                <i
                  className={`fa-solid fa-angle-left ${styles.item}`}
                  onClick={() => handleChangePage(-1)}
                />
              </li>
            ) : (
              <li>
                <i className={styles.item}></i>
              </li>
            )}
            {pageNumbers.map((number) => (
              <li key={number}>
                <div
                  className={`${styles.item} ${
                    currentPage === number ? styles.current : null
                  }`}
                  onClick={currentPage!==number?(() => handleChangePage(number)):null}
                >
                  {number}
                </div>
              </li>
            ))}
            {currentPage !==
              Math.ceil(totalProductsLength / productsPerPage)? (
                <li>
                <i
                  className={`fa-solid fa-angle-right ${styles.item}`}
                  onClick={() => handleChangePage(0)}
                />
              </li>
            ) : (
              <li>
                <i className={styles.item}></i>
              </li>
            )}
          </ul>
        </div>
      )}
    </>
  );
}

export default memo(Pagination);
