import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useCookies } from 'react-cookie'
import axios from 'axios'
import { useCartState } from '../../context';

axios.defaults.withCredentials = true;


function NavigationCartList() {
    const [cartList, setCartList] = useState([])
    const [cookies] = useCookies(['user'])
    const { cart } = useCartState()

    useEffect(() => {
        if(cookies.user === undefined) {
            setCartList([])
        } else {
            axios({
                url: "http://localhost/server/cart.php",
                method: "GET"
            })
            .then(response => {
                const result = response.data;
                setCartList(result);
            })
        }
    }, [cart])
    
    return (
        <div className="nav__cartlist-container">
            <div className="nav__cartlist">
                <i className="fa-solid fa-cart-shopping nav__cartlist-icon" />
                {cartList.length > 0 && <div className="cart_number_badge">{cartList.length}</div>}
                <div className="nav__cartlist-box">
                    {cartList.length === 0 ?
                        <div className="nav__cartlist-empty">
                            <img src={process.env.PUBLIC_URL + "/resources/img/empty_cartlist.png"} alt="empty_cartlist" />
                            <span>Cha c sn phm</span>
                        </div> :
                        <>
                            <div className="nav__cartlist-box-title">
                                <p>Sn phm mi thm</p>
                            </div>
                            <div className="nav__cartlist-item-container">
                                {cartList.map((item, index)=> (
                                    <Link to={`/product/${item.PRODUCT_ID}`} key={index} className="cartlist-item-link">
                                        <div className="cartlist__item">
                                            <div className="carlist__item-header" style={{ backgroundImage: `url(${item.PRODUCT_IMAGE})`, backgroundSize: "contain", backgroundRepeat: "no-repeat", backgroundPosition: "50%"}}></div>
                                            <div className="cartlist__item-body">
                                                <div className="cartlist__item-name-wrapper">
                                                    <span className="cartlist__item-name">{item.PRODUCT_NAME}</span>
                                                </div>
                                                <div className="cartlist__item-price-wrapper">
                                                    <span className="cartlist__item-price">{Number.parseFloat(item.PRODUCT_PRICE).toLocaleString()}</span>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                ))}
                            </div>
                            <div className="nav__carlist-control">
                                <Link to="/cart" className="btn btn--primary">Xem gi hng</Link>
                            </div>
                        </>
                    }
                </div>
            </div>
        </div>
    )
}

export default NavigationCartList