import { Link } from "react-router-dom";
import styles from "./cart.module.css";

import MoneyShow from "../MoneyShow/MoneyShow";

function Item({ itemProps, index, handleProductQuantity, handleCheck, handleDeleteProduct }) {
  return (
    <div key={index} className={`${styles.row} ${styles.item}`}>
      <div className={styles.checkbox_wrapper}>
        <input
          type="checkbox"
          name="item_checkbox"
          className={styles.checkbox}
          checked={itemProps.checked || false}
          onChange={(e) => handleCheck(e, itemProps.PRODUCT_ID)}
        />
      </div>
      <div className={styles.item_info_container}>
        <Link
          to={`/product/${itemProps.PRODUCT_ID}`}
          className={styles.item_img_wrapper}
          style={{ 
            backgroundImage: `url(${itemProps.PRODUCT_IMAGE})`, backgroundSize: "contain", backgroundRepeat: "no-repeat", backgroundPosition: "50%"
          }}
        >
        </Link>
        <Link
          to={`/product/${itemProps.PRODUCT_ID}`}
          className={styles.item_name_wrapper}
        >
          {itemProps.PRODUCT_NAME}
        </Link>
      </div>
      <div className={`${styles.item_price_container}`}>
        <div>
          {itemProps.DISCOUNT !== 0 ? (
            <>
              <span className={styles.item_price_base}>
                {MoneyShow({price: itemProps.PRODUCT_PRICE})}
              </span>
              <span className={styles.item_price_new}>
                {MoneyShow({price: itemProps.PRODUCT_PRICE, discount: itemProps.DISCOUNT})}
              </span>
            </>
          ) : (
            <span className={styles.item_price_new}>
              {MoneyShow({price: itemProps.PRODUCT_PRICE})}
            </span>
          )}
        </div>
      </div>
      <div className={`${styles.item_quantity_control_wrapper}`}>
        <div className="quantity_control">
          <button
            className="quantity_control_button"
            onClick={() => handleProductQuantity(itemProps.PRODUCT_ID, "minus")}
          >
            -
          </button>
          <input
            type="number"
            className="quantity_control_input"
            id="product_quantity"
            value={itemProps.QUANTITY}
            onChange={(e) => handleProductQuantity(itemProps.PRODUCT_ID, "", e)}
          />
          <button
            className="quantity_control_button"
            onClick={() => handleProductQuantity(itemProps.PRODUCT_ID, "add")}
          >
            +
          </button>
        </div>
      </div>
      <div className={styles.item_price_total}>
        <span>{MoneyShow({price: itemProps.PRODUCT_PRICE, quantity: itemProps.QUANTITY, discount: itemProps.DISCOUNT})}</span>
      </div>
      <div className={styles.item_action}>
        <span onClick={() => handleDeleteProduct(itemProps.PRODUCT_ID)}>Xo</span>
      </div>
    </div>
  );
}

export default Item;
