import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";
import styles from "./checkout.module.css";
import OnlinePaymentForm from "../../components/Forms/onlinepayment";
import EditForm from "../../components/Forms/Edit/Edit"
import axios from "axios";
import MoneyShow from "../../components/MoneyShow/MoneyShow";
import PurchasePopup from "../../components/CompletePurchase/CompletePurchase"
import { clearCart, useCartState } from '../../context';
import { UserDetailCheck } from '../../js/UserDetailCheck.js'

const initialBill = {
  USER_ADDRESS: "",
  shipmentPrice: "Min ph",
  paymentMethod: "cod",
};

const initPopupState = {isOpen: false, type:"", message: "", button1: null, button2: null}

function Checkout() {
  const [totalBill, setTotalBill] = useState(initialBill);
  const [onEditing, setOnEditing] = useState(false)
  const [CreditCard, setCreditCard] = useState({});
  const [cookies, setCookie, removeCookie] = useCookies();
  const [isShown, setIsShown] = useState(false)
  const [CompletePopup, setCompletePopup] = useState(initPopupState)
  const { dispatch } = useCartState()
  const navigate = useNavigate();
  
  const handleEditUserAddress = data => {
    setTotalBill(prev => ({
      ...prev, 
      ...data
    }))
  }
  
  const handlePurchase = () => {
    if(UserDetailCheck(cookies.user)) {
      axios({
        method: "POST",
        url: "http://localhost/server/invoice.php",
        data: JSON.stringify(totalBill)
      })
      .then(response => {
        const result = response.data;
        if(result.SUCCEES) {
          clearCart(dispatch);
          setCookie("cart", [], {path: "/"});
          setCookie("creditcard", {}, {path: "/"})
          setCompletePopup({
            isOpen: true,
            type: "SUCCESS",
            message: "Thanh ton thnh cng, m n hng: " + result.MESSAGE,
            button1: navigateToHome,
            button2: navigateToUserInvoices
          })
        }
      })
    } else {
      setCompletePopup({isOpen: true, type:"failure", message: "Vui lng nhp y  thng tin trc khi tin hnh thanh ton", button1: () => setCompletePopup(initPopupState), button2: () => navigate("/user/profile")})
    }
  }

  const navigateToHome = () => {
    navigate("/")
  }

  const navigateToUserInvoices = () => {
    navigate("/user/purchase?status=0")
  }

  useEffect(() => {
    const CartRequestURL = "http://localhost/server/cart.php";
    const AuthenicRequestURL = "http://localhost/server/login-authenic.php";

    const RequestUserCart = async () => {
      axios(CartRequestURL).then((response) => {
        const products = response.data;
        setTotalBill((totalBill) => ({
          ...totalBill,
          USER_ID: cookies.user.USER_ID,
          PRODUCTS: products,
          USER_FULL_NAME: cookies.user.USER_FULL_NAME,
          USER_ADDRESS: cookies.user.USER_ADDRESS,
          USER_PHONE_NUMBER: cookies.user.USER_PHONE_NUMBER,
          ORDER_GRAND_TOTAL: products.reduce(
            (acc, product) =>
              acc +
              ((product.PRODUCT_PRICE * (100 - product.DISCOUNT)) / 100) *
                product.QUANTITY,
            0
          ),
        }));
        setCreditCard(cookies.creditcard || {})
      });
    };

    
    axios({
      url: AuthenicRequestURL,
      method: "GET",
    }).then((response) => {
      const result = response.data;
      if (result.SUCCESS) {
        if (cookies.cart.length === 0) {
          alert("Vui lng chn sn phm trc khi thanh ton");
          navigate("/");
        } else {
          RequestUserCart();
        }
      } else {
        navigate("/login");
      }
    });
  }, []);

  const handleChangeCurrentPaymentMethod = (method) => {
    setTotalBill((prev) => ({ ...prev, paymentMethod: method }));
  };

  return (
    <>
      <div className={styles.top_nav}>
        <div className="grid wide">
          <div className="row">
            <div className="col c-12">
              <div className={styles.top_nav_left_container}>
                <Link to="/">
                  <div className={styles.top_nav_item}>Trang ch</div>
                  <div
                    className={`${styles.top_nav_item} ${styles.item_separator}`}
                  >
                    Thanh ton
                    <i className="fa-solid fa-wallet" />
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="grid wide">
        <div className="row">
          <div className="col c-12">
            <div className={styles.section_container}>
              <div className={styles.shipment_info_container}>
                <div className={styles.shipment_title}>
                  <i className="fa-solid fa-location-dot" />
                  a ch nhn hng
                </div>
                <div className={styles.shipment_content}>
                  <div className={styles.shipment_userinfo_container}>
                    <div className={styles.user_info}>
                      <span>{totalBill.USER_FULL_NAME}</span>
                      <span>({totalBill.USER_PHONE_NUMBER})</span>
                    </div>
                    <div className={styles.user_address}>
                      {totalBill.USER_ADDRESS}
                    </div>
                  </div>
                  <div className={styles.shipment_control_button} onClick={() => setOnEditing(true)}>Thay i</div>
                </div>
              </div>
              <div className={styles.products_show_container}>
                <div className={styles.products_heading}>
                  <div className={styles.info_heading}>Sn phm</div>
                  <div className={styles.price_heading}>n gi</div>
                  <div className={styles.quantity_heading}>S lng</div>
                  <div className={styles.totalPrice_heading}>Thnh tin</div>
                </div>
                <div className={styles.products_container}>
                  {totalBill.PRODUCTS && totalBill.PRODUCTS.map((item) => (
                    <div key={item.PRODUCT_ID} className={styles.item}>
                      <div className={styles.item_info}>
                        <Link
                          to={`/product/${item.PRODUCT_ID}`}
                          className={styles.item_image}
                          style={{backgroundImage: `url(${item.PRODUCT_IMAGE})`, backgroundSize: "contain", backgroundRepeat: "no-repeat", backgroundPosition: "50%"}}
                        > 
                        </Link>
                        <Link to={`/${item.id}`} className={styles.item_name}>
                          <span>{item.PRODUCT_NAME}</span>
                        </Link>
                      </div>
                      <div className={styles.item_price}>
                      {MoneyShow({
                          price: item.PRODUCT_PRICE,
                          quantity: item.QUANTITY,
                          discount: item.DISCOUNT,
                        })}
                      </div>
                      <div className={styles.item_quantity}>
                        {item.QUANTITY}
                      </div>
                      <div className={styles.item_totalPrice}>
                        {MoneyShow({
                          price: item.PRODUCT_PRICE,
                          quantity: item.QUANTITY,
                          discount: item.DISCOUNT,
                        })}
                      </div>
                    </div>
                  ))}
                </div>
                <div className={styles.products_total_container}>
                  <div className={styles.empty}></div>
                  <div className={styles.products_total_content}>
                    <span className={styles.title}>Tng s tin:</span>
                    <span className={styles.number}>
                      {MoneyShow({ price: totalBill.ORDER_GRAND_TOTAL })}
                    </span>
                  </div>
                </div>
              </div>

              <div className={styles.payment_container}>
                <div className={styles.payment_addingvouchers_container}>
                  <div className={styles.title}>
                    <i className="fa-solid fa-ticket-simple" />
                    M gim gi ( Tm thi khng hot ng )
                  </div>
                  <div className={styles.control}>
                    <div className={styles.button}>Chn m gim gi</div>
                  </div>
                </div>
                <div className={styles.payment_method_container}>
                  <div className={styles.title}>Phng thc thanh ton</div>
                  <div className={styles.payment_methods}>
                    <div
                      className={`${styles.item} ${
                        totalBill.paymentMethod === "online"
                          ? styles.active
                          : ""
                      }`}
                      onClick={() => handleChangeCurrentPaymentMethod("online")}
                    >
                      Th tn dng/Ghi n
                    </div>
                    <div
                      className={`${styles.item} ${
                        totalBill.paymentMethod === "cod" ? styles.active : ""
                      }`}
                      onClick={() => handleChangeCurrentPaymentMethod("cod")}
                    >
                      Thanh ton khi nhn hng
                    </div>
                  </div>
                  <div className={styles.payment_method_show}>
                    {totalBill.paymentMethod === "online" ? (
                      !CreditCard.cardnumber ? (
                        <button
                          className={`btn ${styles.payment_method_sub_btn}`}
                          onClick={() => setIsShown(true)}
                        >
                          &#43; Thm th
                        </button>
                      ) : 
                      <div className={styles.creditcard_info}>
                        <div className={styles.item}>Thanh ton Online</div>
                        <div className={styles.item}>Tn ch th: <span className={styles.value}>{CreditCard.cardowner}</span> </div>
                        <div className={styles.item}>S th: <span className={styles.value}>{CreditCard.cardnumber}</span></div>
                      </div>
                    ) : "" || totalBill.paymentMethod === "cod" ? (
                      <div className={styles.payment_method_content}>
                        Thanh ton khi nhn hng ( min ph vn chuyn )
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
                <div className={styles.payment_controller}>
                  <div className={styles.content}>
                    <div
                      className={`${styles.container} ${styles.title_container}`}
                    >
                      <div className={styles.item}>Tng tin hng</div>
                      <div className={styles.item}>Ph vn chuyn</div>
                      <div className={styles.item}>Tng thanh ton:</div>
                    </div>
                    <div
                      className={`${styles.container} ${styles.amount_container}`}
                    >
                      <div className={styles.item}>
                        {MoneyShow({ price: totalBill.ORDER_GRAND_TOTAL })}
                      </div>
                      <div className={styles.item}>
                        {totalBill.shipmentPrice
                          ? totalBill.shipmentPrice
                          : null}
                      </div>
                      <div className={`${styles.item} ${styles.main}`}>
                        {MoneyShow({ price: totalBill.ORDER_GRAND_TOTAL })}
                      </div>
                    </div>
                  </div>
                  <div className={styles.controller}>
                    <div
                      className={`btn btn--primary ${
                        (totalBill.paymentMethod === "cod" || cookies.creditcard) ? null : "disabled"
                      } ${styles.controller_button}`}
                      onClick={() => handlePurchase()}
                    >
                      t hng
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {
        isShown && <OnlinePaymentForm setIsShown={setIsShown} setCreditCard={setCreditCard} />
      }
      {
        onEditing && <EditForm setOnEditing={setOnEditing} handleEditUserAddress={handleEditUserAddress} ></EditForm>
      }
      {
        CompletePopup.isOpen && <PurchasePopup type={CompletePopup.type} Invoice={{ORDER_ID: CompletePopup.message}} message={CompletePopup.message} button1={CompletePopup.button1} button2={CompletePopup.button2}/>
      } 
    </>
  );
}

export default Checkout;
