import { useEffect } from 'react'
import { useNavigate, Link, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { loginUser, useAuthDispatch } from '../../context'
import { useCookies } from 'react-cookie'

function LoginSite() {
  const [cookies, setCookie] = useCookies(['user']);
  const {
    register,
    handleSubmit, 
    formState: { errors },
  } = useForm();
  const dispatch = useAuthDispatch();
  const navigate = useNavigate();
  const location = useLocation()


  let from = location.state?.from?.pathname || "/";

  const handleLogin = async (data) => {
    let payload = {...data}
    try {
      let response = await loginUser(dispatch, payload)
      if(response.user) {
        setCookie("user", response.user, {path: "/"})
        setCookie("cart", [], {path: "/"})
        navigate(from, { replace: true})  
      } else {
        alert(response.MESSAGE)
      }
    } catch(error) {  
    }
  } 

  return (
    <>
      <div className="loginsite__background">
        <div className="loginsite__box">
          <div className="loginsite__route-back-wrapper">
            <i className="fa-solid fa-angle-left loginsite__route-back-icon"></i>
            <Link to="/" className="loginsite__route-back-button">
              Tr li
            </Link>
          </div>
          <div className="loginsite__header-area">
            <div className="loginsite__title">ng nhp</div>
            <div className="loginsite__swap-button-wrapper">
              <Link
                to="/signup"
                className="loginsite__swap-button btn btn--primary"
              >
                ng k
              </Link>
            </div>
          </div>

          <div className="loginsite__input-container">
            <form onSubmit={handleSubmit(handleLogin)}>
              <div className="loginsite__item">
                <input
                  type="text"
                  className="loginsite__input"
                  name="user_name"
                  defaultValue={location?.state?.USER_NAME || ''}
                  placeholder="Tn ti khon/S in thoi/Email"
                  {...register("USER_NAME", {
                    required: { 
                      value: true,
                      message: "khng c b trng"
                    },
                    minLength: { 
                      value: 10,
                      message: "khng hp l"
                    }
                  })}
                />
              </div>
              <div className="loginsite__item">
                <input
                  type="password"
                  className="loginsite__input"
                  name="user_password"
                  placeholder="Mt khu"
                  {...register("USER_PASSWORD", {
                    required: {
                      value: true,
                      message: "khng c b trng"
                    },
                    minLength: {
                      value: 8,
                      message: "phi t 8 k t tr ln"
                    }
                  })}
                />
              </div>
              {
                Object.keys(errors).length !== 0 &&
                <ul className="error_container">
                  {errors.USER_NAME && <li>Ti khon: {errors.USER_NAME.message}</li>}
                  {errors.USER_PASSWORD && <li>Mt khu: {errors.USER_PASSWORD.message}</li>}
                </ul>
              }
              <div className="loginsite__confirm-control">
                <button className="btn btn--primary">
                  ng nhp
                </button>
              </div>
            </form>
          </div>

          <div className="horizontal">Hoc</div>

          <div className="loginsite__control-container">
            <Link to="/" className="loginsite__control-button">
              <img
                src={process.env.PUBLIC_URL + "/resources/img/logo/facebook.webp"}
                alt="Facebook"
                className="loginsite__control-icon"
              />
              Facebook
            </Link>
            <Link to="/" className="loginsite__control-button">
              <img
                src={process.env.PUBLIC_URL + "/resources/img/logo/google.png"}
                alt="Google"
                className="loginsite__control-icon"
              />
              Google
            </Link>
          </div>
        </div>
      </div>
    </>
  );
}

export default LoginSite;
