import React from "react";
import { useNavigate } from 'react-router-dom'
import styles from "./Orders.module.css";
import "./Orders.css";

const OrderStatusRender = ({ ORDER_STATUS }) => {
  let TYPE = "";
  let CONTENT = "";

  ORDER_STATUS = Number.parseInt(ORDER_STATUS);

  switch (ORDER_STATUS) {
    case 2:
      TYPE = styles.completed;
      CONTENT = " giao";
      break;
    case 1:
      TYPE = styles.delivering;
      CONTENT = "ang giao";
      break;
    case 0:
      TYPE = styles.pending;
      CONTENT = "Ch x l";
      break;
    case 3:
      TYPE = styles.canceled;
      CONTENT = " hu";
      break;
    default:
      return;
  }

  return <div className={`${styles.order_status} ${TYPE}`}>{CONTENT}</div>;
};

function Orders({ Orders, type = "canceled" }) {
  const navigate = useNavigate()
  let status;

  switch (type) {
    case "canceled":
      status = {
        title: " b hu",
        type: "canceled",
      };
      break;
    case "pending":
      status = {
        title: "Cha duyt",
        type: "pending",
      };
      break;
    case "approved":
      status = {
        title: " duyt",
        type: "approved",
      };
      break;
    case "delivering":
      status = {
        title: "ang giao",
        type: "delivering",
      };
    case "completed":
      status = {
        title: " giao",
        type: "completed",
      };
      break;
  }

  const navigateToSelectedOrder = (ORDER_ID) => {
    navigate("/admin/orders", {state: ORDER_ID})
  }

  return (
    <table className={styles.table}>
      <thead>
        <tr>
          <th>Khch hng</th>
          <th>Ngy t hng</th>
          <th>Tnh trng n hng</th>
        </tr>
      </thead>
      <tbody>
        {Orders && Orders.map((Order, index) => (
          <tr key={index} onClick={() => navigateToSelectedOrder(Order.ORDER_ID)}>
            <td>
              <div className={styles.user}>
                <img className={styles.avatar} src={Order.USER_IMAGE} />
                <div className={styles.name}>{Order.CUSTOMER_FULL_NAME}</div>
              </div>
            </td>
            <td>{Order.ORDER_CREATED_AT}</td>
            <td>
              <OrderStatusRender ORDER_STATUS={Order.ORDER_STATUS} />
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}

export default Orders;
