import axios from "axios";
import React from "react";
import { useState, useEffect, useRef } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import styles from "./AdminUsers.module.css";

const RoleRender = (role) => {
  switch (role) {
    case "0":
      return "Qun tr h thng";
    case "1":
      return "Qun tr ca hng";
    case "2":
      return "Khch hng";
    default:
      return "Khng xc nh";
  }
};

const StatusRender = (status) => {
  return status === "0" ? "Hot ng" : "Khng hot ng";
};

const UserStatusList = [
  {
    STATUS_ID: 0,
    STATUS_NAME: "Qun tr h thng",
    STATUS_CLASSNAME: styles.admin,
  },
  {
    STATUS_ID: 1,
    STATUS_NAME: "Qun tr ca hng",
    STATUS_CLASSNAME: styles.shopowner,
  },
  {
    STATUS_ID: 2,
    STATUS_NAME: "Khch hng",
    STATUS_CLASSNAME: styles.customer,
  }
];

const UserRoleList = [
  {
    STATUS_ID: 0,
    STATUS_NAME: "Hot ng",
    STATUS_CLASSNAME: styles.active,
  },
  {
    STATUS_ID: 1,
    STATUS_NAME: "Khng hot ng",
    STATUS_CLASSNAME: styles.disabled,
  }
];

const getcurrentTimestamp = () => {
  var currentDate = new Date();

  return (
    currentDate.getDate() +
    "-" +
    currentDate.getHours() +
    "-" +
    currentDate.getFullYear() +
    " " +
    currentDate.getHours() +
    ":" +
    currentDate.getMinutes() +
    ":" +
    currentDate.getSeconds()
  );
};

const DefaultUserDetails = {
  USER_REGISTERED_AT: getcurrentTimestamp(),
  USER_ROLE: 2,
  IS_DISABLED: 0,
};

function AdminUser() {
  const [Users, setUsers] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
  const [SelectedUser, setSelectedUser] = useState({});
  const [UploadedImage, setUploadedImage] = useState({});
  const [AddingModal, setAddingModal] = useState(false);
  const [Filter, setFilter] = useState({});
  const [IsIncreasing, setIsIncreasing] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();
  const location = useLocation();
  const RoleList = useRef()
  const StatusList = useRef();

  useEffect(() => {
    axios(`http://localhost/server/user.php${location.search}`)
    .then(
      (response) => {
        const result = response.data;
        if (result.SUCCESS) {
          setUsers(result.USERS);
        }
      }
    );
  }, [searchParams]);

  const handleChangeCurrentFilter = (filter_name, filter_value) => {
    if (filter_name) {
      const newFilter = { ...Filter, [filter_name]: filter_value };
      setSearchParams(newFilter);
      setFilter(newFilter);
      setIsIncreasing((prev) => !prev);
    } else {
      setSearchParams({});
      setFilter({});
      setIsIncreasing(false);
    }
  };

  const handleOpenStatusList = () => {
    StatusList.current.classList.toggle(`${styles.open}`);
  };

  const handleOpenRoleList = () => {
    RoleList.current.classList.toggle(`${styles.open}`);
  };

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const [Modal, setModal] = useState(false);
  const {
    register: adding_register,
    handleSubmit: adding_handleSubmit,
    reset: adding_reset,
    formState: { errors: adding_errors },
  } = useForm();

  // Clear the inputs when exit edit
  useEffect(() => {
    reset({
      keepValues: false,
    });
  }, [isEditing]);

  const UploadImage = (USER_ID) => {
    const formData = new FormData();
    formData.append("image", UploadedImage.file);
    formData.append("USER_ID", JSON.stringify({ USER_ID: USER_ID }));
    axios({
      method: "POST",
      url: `http://localhost/server/update-user.php?USER_ID=${USER_ID}`,
      data: formData,
      header: {
        "Content-Type": "multipart/form-data",
      },
    }).then((response) => {
      const result = response.data;
      if (result.SUCCESS) {
        setUploadedImage({});
      }
    });
  };

  const UpdateUser = (data) => {
    data = { ...SelectedUser, ...data };
    axios({
      method: "POST",
      url: `http://localhost/server/update-user.php?admin&USER_ID=${data.USER_ID}`,
      data: data,
      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      const result = response.data;
      if (result.Status === "OK") {
        UploadedImage && UploadImage(data.USER_ID);
        setUsers((prev) =>
          prev.map((User) =>
            User.USER_ID === data.USER_ID
              ? { ...data, USER_IMAGE: UploadedImage.name }
              : User
          )
        );
      }
    });
    setIsEditing((prev) => !prev);
  };

  const handleOpenModal = (USER_ID) => {
    const User = Users.filter((User) => User.USER_ID === USER_ID)[0];
    setSelectedUser(User);
    setModal(true);
  };

  const handleCloseModal = () => {
    setIsEditing(false);
    setModal(false);
  };

  const handleOpenAddingPopup = () => {
    setAddingModal(true);
  };

  const handleCloseAddingPopup = () => {
    setAddingModal(false);
  };

  // Close Modal when Success Update
  useEffect(() => {
    setModal(false);
  }, [Users]);

  const updateImageState = (event) => {
    // create temp image url to show
    let file = event.target.files[0];
    const name = URL.createObjectURL(file);

    setUploadedImage({
      name: name,
      file: file,
    });
  };

  const handleRegister = (data) => {
    axios({
      method: "POST",
      url: "http://localhost/server/login.php?register",
      data: data,
    }).then((response) => {
      alert(response.data.MESSAGE);
      setUsers((prevUser) => [...prevUser, data]);
    });
  };

  console.log(DefaultUserDetails);

  return (
    <>
      <div className={styles.section_container}>
        <div className={styles.product_adding_pane}>
          <button
            className={`btn btn--primary`}
            onClick={() => handleOpenAddingPopup()}
          >
            Thm ngi dng
          </button>
        </div>
        <table className={styles.users_table}>
          <thead>
            <tr>
              <th>Ngi dng</th>
              <th>Email</th>
              <th
                style={{ cursor: "pointer" }}
                onClick={() =>
                  handleChangeCurrentFilter(
                    "sortby",
                    IsIncreasing ? "desc" : "asc"
                  )
                }
              >
                Thnh lp ngy{" "}
                <i
                  className={`${
                    IsIncreasing
                      ? "fa-solid fa-angle-up"
                      : "fa-solid fa-angle-down"
                  } ${styles.filter_button}`}
                />
              </th>
              <th
                className={styles.has_dropdown}
                onClick={() => handleOpenRoleList()}
                style={{ cursor: "pointer" }}
              >
                Vai tr
                <i
                  className={`fa-solid fa-angle-down ${styles.filter_button}`}
                />
                <div
                  className={`${styles.status_list_container} ${styles.dropdown}`}
                  ref={RoleList}
                >
                  <ul className={styles.status_list}>
                    <li>
                      <div
                        to={`/admin/orders`}
                        className={styles.status_item_link}
                        onClick={() => handleChangeCurrentFilter()}
                      >
                        Tt c
                      </div>
                    </li>
                    {UserStatusList.map((Status) => (
                      <li key={Status.STATUS_ID}>
                        <div
                          className={`${Status.STATUS_CLASSNAME} ${styles.status_item_link} ${styles.order_status}`}
                          onClick={() =>
                            handleChangeCurrentFilter(
                              "role",
                              Status.STATUS_ID
                            )
                          }
                        >
                          {Status.STATUS_NAME}
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              </th>
              <th
                className={styles.has_dropdown}
                onClick={() => handleOpenStatusList()}
                style={{ cursor: "pointer" }}
              >
                Trng thi
                <i
                  className={`fa-solid fa-angle-down ${styles.filter_button}`}
                />
                <div
                  className={`${styles.status_list_container} ${styles.dropdown}`}
                  ref={StatusList}
                >
                  <ul className={styles.status_list}>
                    <li>
                      <div
                        to={`/admin/orders`}
                        className={styles.status_item_link}
                        onClick={() => handleChangeCurrentFilter()}
                      >
                        Tt c
                      </div>
                    </li>
                    {UserRoleList.map((Status) => (
                      <li key={Status.STATUS_ID}>
                        <div
                          className={`${Status.STATUS_CLASSNAME} ${styles.status_item_link} ${styles.order_status}`}
                          onClick={() =>
                            handleChangeCurrentFilter(
                              "status",
                              Status.STATUS_ID
                            )
                          }
                        >
                          {Status.STATUS_NAME}
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              </th>
              <th>La chn</th>
            </tr>
          </thead>
          <tbody>
            {Users.map((User) => (
              <tr key={User.USER_ID}>
                <td>
                  <div className={styles.user}>
                    <img
                      src={
                        User.USER_IMAGE || process.env.PUBLIC_URL + 
                        "/resources/img/1024px-User-avatar.svg.png"
                      }
                      alt="nh ngi dng"
                      className={styles.user_img}
                    />
                    <div className={styles.user_name}>{User.USER_NAME}</div>
                  </div>
                </td>
                <td>{User.USER_EMAIL}</td>
                <td>{User.USER_REGISTERED_AT}</td>
                <td>{RoleRender(User.USER_ROLE)}</td>
                <td style={{ textAlign: "center" }}>
                  <div
                    className={`${styles.user_status} ${
                      User.IS_DISABLED === "0" ? styles.active : styles.disabled
                    }`}
                  >
                    {User.IS_DISABLED === "0" ? "Hot ng" : "Khng hot ng"}
                  </div>
                </td>
                <td>
                  <i
                    className={`fa-solid fa-ellipsis ${styles.user_control}`}
                    onClick={() => handleOpenModal(User.USER_ID)}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {Modal && (
        <div className={styles.profile_modal}>
          <div className={styles.profile_container}>
            <form onSubmit={handleSubmit(UpdateUser)}>
              <div className={styles.header}>
                <span className={styles.hero_title}>Thng tin ngi dng</span>
              </div>
              <div className={styles.body}>
                <div className={styles.user_profile_container}>
                  <div className="grid wide">
                    <div className="row">
                      <div className="col c-3">
                        <div
                          className={styles.user_avatar_wrapper}
                          style={{
                            backgroundImage: `url(${
                              (UploadedImage && UploadedImage.name) ||
                              SelectedUser.USER_IMAGE
                            })`,
                            backgroundSize: "cover",
                            backgroundRepeat: "no-repeat",
                            backgroundPosition: "50%",
                          }}
                        >
                          {isEditing && (
                            <div className={styles.user_avatar_controller}>
                              <button
                                type="button"
                                className="btn"
                                onClick={() =>
                                  document
                                    .querySelector(`.${styles.input_file}`)
                                    .click()
                                }
                              >
                                Chn nh
                              </button>
                              <input
                                type="file"
                                onChange={(e) => updateImageState(e)}
                                className={styles.input_file}
                              />
                            </div>
                          )}
                        </div>
                      </div>
                      <div className="col c-9">
                        <div className={styles.user_informations}>
                          <div className={styles.info_area}>
                            <span className={styles.label}>ID ngi dng</span>
                            <span className={styles.content}>
                              {SelectedUser.USER_ID}
                            </span>
                          </div>
                          <div className={styles.info_area}>
                            <span className={styles.label}>Tn ti khon</span>
                            {isEditing ? (
                              <input
                                type="text"
                                className={styles.content_editing}
                                defaultValue={SelectedUser.USER_NAME}
                                {...register("USER_NAME")}
                              />
                            ) : (
                              <span className={styles.content}>
                                {SelectedUser.USER_NAME}
                              </span>
                            )}
                          </div>
                          <div className={styles.info_area}>
                            <span className={styles.label}>Email</span>
                            {isEditing ? (
                              <input
                                type="text"
                                className={styles.content_editing}
                                defaultValue={SelectedUser.USER_EMAIL}
                                {...register("USER_EMAIL")}
                              />
                            ) : (
                              <span className={styles.content}>
                                {SelectedUser.USER_EMAIL}
                              </span>
                            )}
                          </div>
                        </div>
                      </div>
                      <div className="col c-12">
                        <div
                          className={styles.user_informations}
                          style={{ marginTop: "30px" }}
                        >
                          {isEditing ? (
                            <div className={styles.info_area_group}>
                              <div
                                className={`${styles.info_area} ${styles.mini_item}`}
                              >
                                <span className={styles.label}>H v tn</span>
                                <input
                                  type="text"
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.USER_FULL_NAME}
                                  {...register("USER_FULL_NAME")}
                                />
                              </div>
                            </div>
                          ) : (
                            <div className={`${styles.info_area}`}>
                              <span className={styles.label}>H v tn</span>
                              <span className={styles.content}>
                                {SelectedUser.USER_FULL_NAME}
                              </span>
                            </div>
                          )}
                          <div className={styles.info_area_group}>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>
                                Ngy sinh (MM/DD/YYYY)
                              </span>
                              {isEditing ? (
                                <input
                                  type="date"
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.USER_DOB}
                                  {...register("USER_DOB")}
                                />
                              ) : (
                                <span className={styles.content}>
                                  {SelectedUser.USER_DOB}
                                </span>
                              )}
                            </div>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>Gii tnh</span>
                              {isEditing ? (
                                <select
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.USER_GENDER}
                                  {...register("USER_GENDER")}
                                >
                                  <option value="0">Nam</option>
                                  <option value="1">N</option>
                                </select>
                              ) : (
                                <span className={styles.content}>
                                  {SelectedUser.USER_GENDER === "0"
                                    ? "Nam"
                                    : "N"}
                                </span>
                              )}
                            </div>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>
                                S in thoi
                              </span>
                              {isEditing ? (
                                <input
                                  type="number"
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.USER_PHONE_NUMBER}
                                  {...register("USER_PHONE_NUMBER")}
                                />
                              ) : (
                                <span className={styles.content}>
                                  {SelectedUser.USER_PHONE_NUMBER}
                                </span>
                              )}
                            </div>
                          </div>
                          <div className={styles.info_area_group}>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>ng k ngy</span>
                              <span className={styles.content}>
                                {SelectedUser.USER_REGISTERED_AT}
                              </span>
                            </div>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>Vai tr</span>
                              {isEditing ? (
                                <select
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.USER_ROLE}
                                  {...register("USER_ROLE")}
                                >
                                  <option value="2">Khch hng</option>
                                  <option value="1">Qun tr ca hng</option>
                                  <option value="0">Qun tr h thng</option>
                                </select>
                              ) : (
                                <span className={styles.content}>
                                  {RoleRender(SelectedUser.USER_ROLE)}
                                </span>
                              )}
                            </div>
                            <div
                              className={`${styles.info_area} ${styles.mini_item}`}
                            >
                              <span className={styles.label}>Trng thi</span>
                              {isEditing ? (
                                <select
                                  className={styles.content_editing}
                                  defaultValue={SelectedUser.IS_DISABLED}
                                  {...register("IS_DISABLED")}
                                >
                                  <option value="0">Hot ng</option>
                                  <option value="1">Khng hot ng</option>
                                </select>
                              ) : (
                                <span
                                  className={`${styles.content} ${
                                    SelectedUser.IS_DISABLED === "0"
                                      ? styles.user_status_active
                                      : styles.user_status_disabled
                                  }`}
                                >
                                  {StatusRender(SelectedUser.IS_DISABLED)}
                                </span>
                              )}
                            </div>
                          </div>
                          <div className={`${styles.info_area}`}>
                            <span className={styles.label}>a ch</span>
                            {isEditing ? (
                              <textarea
                                type="text"
                                className={styles.content_editing}
                                defaultValue={SelectedUser.USER_ADDRESS}
                                {...register("USER_ADDRESS")}
                              />
                            ) : (
                              <span className={styles.content}>
                                {SelectedUser.USER_ADDRESS}
                              </span>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className={styles.profile_modal_controllers}>
                <button
                  type="button"
                  className="btn"
                  onClick={() => handleCloseModal()}
                >
                  Tr v
                </button>
                {!isEditing && (
                  <button
                    className="btn btn--primary"
                    type="button"
                    onClick={() => setIsEditing((prev) => !prev)}
                  >
                    Chnh sa
                  </button>
                )}
                {isEditing && <button className="btn btn--primary">Lu</button>}
              </div>
            </form>
          </div>
        </div>
      )}

      {AddingModal && (
        <div className={styles.modal}>
          <div className={styles.modal_container}>
            <form onSubmit={adding_handleSubmit(handleRegister)}>
              <div className={styles.modal_header}>
                <div className={styles.hero_title}>Thm ngi dng</div>
              </div>
              <div className={styles.modal_body}>
                <div className={styles.text_area}>
                  <div className={styles.label}>Tn ti khon</div>
                  <input
                    type="text"
                    className={styles.input}
                    {...adding_register("USER_NAME", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      minLength: {
                        value: 10,
                        message: "phi t 10 k t tr ln",
                      },
                    })}
                  />
                </div>
                <div className={styles.text_area}>
                  <div className={styles.label}>S in thoi</div>
                  <input
                    type="number"
                    className={styles.input}
                    {...adding_register("USER_PHONE_NUMBER", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      pattern: {
                        value: /(((\+|)84)|0)(3|5|7|8|9)+([0-9]{8})\b/,
                        message: "khng hp l",
                      },
                    })}
                  />
                </div>
                <div className={styles.text_area}>
                  <div className={styles.label}>Email</div>
                  <input
                    type="email"
                    className={styles.input}
                    {...adding_register("USER_EMAIL", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      pattern: {
                        value: /^[\w.+\-]+@gmail\.com$/,
                        message: "khng hp l",
                      },
                    })}
                  />
                </div>
                <div className={styles.text_area}>
                  <div className={styles.label}>Mt khu</div>
                  <input
                    type="password"
                    className={styles.input}
                    {...adding_register("USER_PASSWORD", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      minLength: {
                        value: 8,
                        message: "phi nhiu hn 8 k t",
                      },
                    })}
                  />
                </div>
              </div>
              {Object.keys(adding_errors).length !== 0 && (
                <ul className={styles.errors_container}>
                  {adding_errors.USER_NAME && (
                    <li>Tn ti khon: {adding_errors.USER_NAME.message}</li>
                  )}
                  {adding_errors.USER_PHONE_NUMBER && (
                    <li>
                      S in thoi: {adding_errors.USER_PHONE_NUMBER.message}
                    </li>
                  )}
                  {adding_errors.USER_EMAIL && (
                    <li>Email: {adding_errors.USER_EMAIL.message}</li>
                  )}
                  {adding_errors.USER_PASSWORD && (
                    <li>Mt khu: {adding_errors.USER_PASSWORD.message}</li>
                  )}
                </ul>
              )}
              <div className={styles.modal_controller}>
                <button
                  type="button"
                  className="btn"
                  onClick={() => handleCloseAddingPopup()}
                >
                  Hu
                </button>
                <button className="btn btn--primary">Lu</button>
              </div>
            </form>
          </div>
        </div>
      )}
    </>
  );
}

export default AdminUser;
