import { useState, memo } from 'react'

function SideCategory( { categories, currentCategory: test, handleChangeCurrentFilter } ) {
    const [currentCategory, setCurrentCategory] = useState('all')

    const handleChangeCurrentCategory = (filterName, filterValue) => {
        setCurrentCategory(filterValue)
        handleChangeCurrentFilter(filterName, filterValue)
    }

    return (
        <>
            <div className="sidecategory__container">
                <div className="sidecategory__header">
                    <i className="fa-solid fa-list"/>
                    Danh mc
                </div>
                <div className="sidecategory__body">
                    <ul>
                        <li>
                            <div 
                                onClick={() => handleChangeCurrentCategory("category", "all")}
                                className={"sidecategory__item-link" + (currentCategory==="all"?' current':'')}>
                            {currentCategory === "all"? <i className="fa-solid fa-caret-right sidecategory__current-icon"/>: ''}
                                Tt c sn phm
                            </div>
                        </li>
                        {categories && categories.map((item, index) => 
                            <li key={index}>
                                <div onClick={() => handleChangeCurrentCategory("category", item.CATEGORY_ID)} className={"sidecategory__item-link" + (currentCategory===item.CATEGORY_ID?' current':'')}>
                                    {currentCategory === item.CATEGORY_ID? <i className="fa-solid fa-caret-right sidecategory__current-icon"/>: ''}
                                    {item.CATEGORY_NAME}
                                </div>
                            </li>)
                        }
                    </ul>
                </div>
            </div>
        </>
    )
}

export default memo(SideCategory)