import { useState, useRef } from 'react'
import { Link, useNavigate } from 'react-router-dom'

function NavigationSearch() {
    const [searchTrending, setSearchTrending] = useState([])
    const navigate = useNavigate()
    const searchBox = useRef()

    const handleSearch = () => {
        navigate({
            pathname: '/search',
            search: `?q=${searchBox.current.value}`
        })
    }


    return (
                    <div className="nav__search-container">
                        <div className="nav__search-box">
                            <div className="nav__search-input-wrapper">
                                <input type="text" id="nav__search-input" ref={searchBox} className="nav__search-input"/>
                            </div>
                            <div className="btn btn--primary nav__search-btn" onClick={() => handleSearch()}>
                                <i className="fa-solid fa-magnifying-glass"/>
                            </div>
                        </div>
                        <div className="nav__search-trending-container">
                            <Link to="/" className="nav__search-trending-key">I3-12100f</Link>
                            <Link to="/" className="nav__search-trending-key">I3-12100f</Link>
                            <Link to="/" className="nav__search-trending-key">I3-12100f</Link>
                            <Link to="/" className="nav__search-trending-key">I3-12100f</Link>
                        </div>
                    </div>
    )
}

export default NavigationSearch