import React, { useEffect, useRef } from "react";
import {   useLocation, useSearchParams } from "react-router-dom";
import axios from "axios";
import { useState } from "react";
import styles from "./AdminOrders.module.css";
import MoneyShow from "../../components/MoneyShow/MoneyShow";

const OrderStatusRender = ({ ORDER_STATUS }) => {
  let TYPE = "";
  let CONTENT = "";

  ORDER_STATUS = Number.parseInt(ORDER_STATUS);

  switch (ORDER_STATUS) {
    case 2:
      TYPE = styles.completed;
      CONTENT = " giao";
      break;
    case 1:
      TYPE = styles.delivering;
      CONTENT = "ang giao";
      break;
    case 0:
      TYPE = styles.pending;
      CONTENT = "Ch x l";
      break;
    case 3:
      TYPE = styles.canceled;
      CONTENT = " hu";
      break;
    default:
      return;
  }

  return <div className={`${styles.order_status} ${TYPE}`}>{CONTENT}</div>;
};

const OrderStatus = [
  {
    STATUS_ID: 0,
    STATUS_NAME: "Ch x l",
    STATUS_CLASSNAME: styles.pending,
  },
  {
    STATUS_ID: 1,
    STATUS_NAME: "ang giao",
    STATUS_CLASSNAME: styles.delivering,
  },
  {
    STATUS_ID: 2,
    STATUS_NAME: " giao",
    STATUS_CLASSNAME: styles.completed,
  },
  {
    STATUS_ID: 3,
    STATUS_NAME: " hu",
    STATUS_CLASSNAME: styles.canceled,
  },
];

const Product = ({ PRODUCT }) => (
  <div className={styles.product_item}>
    <div className={styles.product_image_wrapper} style={{backgroundImage: `url(${PRODUCT.PRODUCT_IMAGE})`, backgroundSize: "contain", backgroundRepeat: "no-repeat", backgroundPosition: "50%"}}>
    </div>
    <div className={styles.product_name}>{PRODUCT.PRODUCT_NAME}</div>
  </div>
);

const ProductsRendering = ({ Products, TotalPrice }) => (
  <table className={styles.products_table}>
    <thead>
      <tr>
        <th>M</th>
        <th>Sn phm</th>
        <th>Gi</th>
        <th>S lng</th>
        <th>Thnh tin</th>
      </tr>
    </thead>
    <tbody>
      {Products.map((PRODUCT) => (
        <tr key={PRODUCT.PRODUCT_ID}>
          <td>{PRODUCT.PRODUCT_ID}</td>
          <td>
            <Product PRODUCT={PRODUCT} />
          </td>
          <td>{MoneyShow({ price: PRODUCT.PRODUCT_PRICE })}</td>
          <td>{PRODUCT.PRODUCT_QUANTITY}</td>
          <td>
            {MoneyShow({
              price: PRODUCT.PRODUCT_PRICE,
              quantity: PRODUCT.PRODUCT_QUANTITY,
              discount: PRODUCT.DISCOUNT,
            })}
          </td>
        </tr>
      ))}
      <tr>
        <td colSpan="4">Tng thanh ton:</td>
        <td>{MoneyShow({ price: TotalPrice })}</td>
      </tr>
    </tbody>
  </table>
);

function AdminOrders() {
  const location = useLocation();
  const [Orders, setOrders] = useState([]);
  const [SelectedOrderID, setSelectedOrderID] = useState(location.state || "");
  const [CurrentStatus, setCurrentStatus] = useState(NaN);
  const [isEditing, setIsEditing] = useState(false);
  const [Modal, setModal] = useState(!!location.state || false);
  const [Filter, setFilter] = useState({})
  const [IsIncreasing, setIsIncreasing] = useState(false);
  let [searchParams, setSearchParams] = useSearchParams();
  const StatusList = useRef();

  
  useEffect(() => {
    axios(`http://localhost/server/get-invoices.php${location.search}`).then(
      (response) => {
        const result = response.data;
        if (result.SUCCESS) {
          setOrders(result.INVOICES);
        }
      }
    );
  }, [searchParams]);

  const handleChangeCurrentFilter = (filter_name, filter_value) => {
    if(filter_name) {
      const newFilter = {...Filter, [filter_name]: filter_value}
      setSearchParams(newFilter);
      setFilter(newFilter);
      setIsIncreasing(prev => !prev)
    } else {
      setSearchParams({});
      setFilter({});
      setIsIncreasing(false)
    }
  };

  const handleOpenStatusList = () => {
    StatusList.current.classList.toggle(`${styles.open}`);
  };

  const handleOpenModal = (ORDER_ID) => {
    setSelectedOrderID(ORDER_ID);
    setModal(true);
  };

  const handleCloseModal = () => {
    setSelectedOrderID("")
    setIsEditing(false);
    setModal(false);
  };

  const handleEditOn = (ORDER_STATUS) => {
    setCurrentStatus(ORDER_STATUS);
    setIsEditing(true);
  };

  const handleEditOff = () => {
    setCurrentStatus(NaN);
    setIsEditing(false);
  };

  const handleSaveStatus = () => {
    axios({
      method: "POST",
      url: "http://localhost/server/update-invoice.php",
      data: JSON.stringify({
        ORDER_ID: SelectedOrderID,
        ORDER_STATUS: CurrentStatus,
      }),
    }).then((response) => {
      const result = response.data;
      if (result.SUCCESS) {
        setOrders((prevOrders) =>
          prevOrders.map((Order) =>
            Order.ORDER_ID === SelectedOrderID
              ? { ...Order, ORDER_STATUS: CurrentStatus }
              : Order
          )
        );
        alert(result.MESSAGE);
        handleEditOff();
      } else {
        alert(result.MESSAGE);
      }
    });
  };

  return (
    <>
      <div className={styles.container}>
        <table className={styles.orders_table}>
          <thead>
            <tr>
              <th>STT</th>
              <th>M n hng</th>
              <th>M khch hng</th>
              <th>a ch</th>
              <th 
                style={{ cursor: "pointer" }}
                onClick={() => handleChangeCurrentFilter("sortby", IsIncreasing?"desc":"asc")}
              >
                Thi im t hng
                <i
                  className={`${IsIncreasing?"fa-solid fa-angle-up":"fa-solid fa-angle-down"} ${styles.filter_button}`}
                />
              </th>
              <th
                className={styles.has_dropdown}
                onClick={() => handleOpenStatusList()}
                style={{ cursor: "pointer" }}
              >
                Tnh trng
                <i
                  className={`fa-solid fa-angle-down ${styles.filter_button}`}
                />
                <div
                  className={`${styles.status_list_container} ${styles.dropdown}`}
                  ref={StatusList}
                >
                  <ul className={styles.status_list}>
                    <li>
                      <div
                        to={`/admin/orders`}
                        className={styles.status_item_link}
                        onClick={() => handleChangeCurrentFilter()}
                      >
                        Tt c
                      </div>
                    </li>
                    {OrderStatus.map((Status) => (
                      <li key={Status.STATUS_ID}>
                        <div
                          className={`${Status.STATUS_CLASSNAME} ${styles.status_item_link} ${styles.order_status}`}
                          onClick={() =>
                            handleChangeCurrentFilter(
                              "status",
                              Status.STATUS_ID
                            )
                          }
                        >
                          {Status.STATUS_NAME}
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              </th>
              <th>Tu chn</th>
            </tr>
          </thead>
          <tbody>
            {Orders.map((Order, index) => (
              <tr key={Order.ORDER_ID}>
                <td>{index + 1}</td>
                <td>{Order.ORDER_ID}</td>
                <td>{Order.CUSTOMER_ID}</td>
                <td>{Order.CUSTOMER_ADDRESS}</td>
                <td>{Order.ORDER_CREATED_AT_FORMATED}</td>
                <td>
                  <OrderStatusRender ORDER_STATUS={Order.ORDER_STATUS} />
                </td>
                <td>
                  <i
                    className={`fa-solid fa-ellipsis-vertical ${styles.order_controller}`}
                    onClick={() => handleOpenModal(Order.ORDER_ID)}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {Modal &&
        Orders.map(
          (Order) =>
            Order.ORDER_ID === SelectedOrderID && (
              <div className={styles.modal} key={Order.ORDER_ID}>
                <div className={styles.modal_container}>
                  <div className={styles.header}>
                    <div className={styles.hero_title}>Qun l n hng</div>
                  </div>
                  <div className={styles.body}>
                    <div className={styles.content_container}>
                      <div className={styles.text_area}>
                        <div className={styles.text_label}>M ho n</div>
                        <div className={styles.text_content}>
                          {Order.ORDER_ID}
                        </div>
                      </div>
                      <div className={styles.text_area}>
                        <div className={styles.text_label}>M khch hng</div>
                        <div className={styles.text_content}>
                          {Order.CUSTOMER_ID}
                        </div>
                      </div>
                      <div className={styles.text_area}>
                        <div className={styles.text_label}>Tn khch hng</div>
                        <div className={styles.text_content}>
                          {Order.CUSTOMER_FULL_NAME}
                        </div>
                      </div>
                      <div className={styles.text_area}>
                        <div className={styles.text_label}>S in thoi</div>
                        <div className={styles.text_content}>
                          {Order.CUSTOMER_PHONE_NUMBER}
                        </div>
                      </div>
                      <div className={styles.text_area}>
                        <div className={styles.text_label}>a ch</div>
                        <div className={styles.text_content}>
                          {Order.CUSTOMER_ADDRESS}
                        </div>
                      </div>
                      <div className={styles.product_container}>
                        <ProductsRendering
                          Products={Order.PRODUCTS}
                          TotalPrice={Order.ORDER_GRAND_TOTAL}
                        />
                        <div className={styles.order_controller_container}>
                          <div className={styles.title}>
                            Trnh trng n hng
                          </div>
                          {isEditing ? (
                            <select
                              className={styles.order_status_sellector}
                              defaultValue={Order.ORDER_STATUS}
                              onChange={(e) =>
                                setCurrentStatus(
                                  Number.parseInt(e.target.value)
                                )
                              }
                            >
                              {OrderStatus.map((Status) => (
                                <option
                                  key={Status.STATUS_ID}
                                  value={Status.STATUS_ID}
                                >
                                  {Status.STATUS_NAME}
                                </option>
                              ))}
                            </select>
                          ) : (
                            <div className={styles.order_status}>
                              <OrderStatusRender
                                ORDER_STATUS={Order.ORDER_STATUS}
                              />
                            </div>
                          )}
                          {isEditing ? (
                            <>
                              <div>
                                <div
                                  className={`btn btn--primary ${styles.order_controller}`}
                                  onClick={() => handleSaveStatus()}
                                >
                                  Lu
                                </div>
                                <div
                                  className={`btn ${styles.order_controller}`}
                                  onClick={() => handleEditOff()}
                                >
                                  Hu
                                </div>
                              </div>
                            </>
                          ) : (
                            <div
                              className={`btn btn--primary ${styles.order_controller}`}
                              onClick={() => handleEditOn(Order.ORDER_STATUS)}
                            >
                              Chnh sa
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className={styles.modal_controllers}>
                    <button className="btn" onClick={() => handleCloseModal()}>
                      Tr v
                    </button>
                  </div>
                </div>
              </div>
            )
        )}
    </>
  );
}

export default AdminOrders;
