import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { useCookies } from "react-cookie";
import styles from "./cart.module.css";
import CartItems from "./CartItems";
import MoneyShow from "../MoneyShow/MoneyShow";

function Cart() {
  const [UserCart, setUserCart] = useState([]);
  const [TotalProductInfo, setTotalProductInfo] = useState({});
  const [nextable, setNextable] = useState(false);
  const [cookies, setCookies] = useCookies(["user"]);
  const navigate = useNavigate();

  useEffect(() => {
    const AuthonicRequestURL = "http://localhost/server/login-authenic.php"
    const CartRequestURL = "http://localhost/server/cart.php"

    const AuthonicRequest = axios.get(AuthonicRequestURL)
    const CartRequest = axios.get(CartRequestURL)

    axios.all([AuthonicRequest, CartRequest])
    .then(axios.spread((...props) => {
      const Authonic = props[0].data
      const Cart = props[1].data

      if(Authonic.SUCCESS) {
        setUserCart(Cart)
      } else {
        navigate("/login");

      }
    }))
  }, [cookies]);

  const handleCheck = (e, id) => {
    const { name, checked } = e.target;

    if (name === "allSelect") {
      const newUserCart = UserCart.map((item) => ({ ...item, checked }));
      setUserCart(newUserCart);
    } else {
      const newUserCart = UserCart.map((item) => {
        if (item.PRODUCT_ID === id) {
          item.checked = checked;
        }
        return item;
      });

      setUserCart(newUserCart);
    }
  };

  const handleDeleteProduct = (PRODUCT_ID) => {
    const newCookies = cookies.cart.filter(Product => Product.PRODUCT_ID !== PRODUCT_ID)
    setUserCart(UserCart.filter(Product => Product.PRODUCT_ID !== PRODUCT_ID))
    setCookies("cart", newCookies);
  }

  useEffect(() => {
    const newArray = UserCart.reduce(
      (prev, item) => {
        if (item.checked) {
          return {
            products: [
              ...prev.products,
              {
                id: item.PRODUCT_ID,
                title: item.PRODUCT_NAME,
                image: item.PRODUCT_IMAGE,
                price: item.PRODUCT_PRICE,
                quantity: item.QUANTITY,
              },
            ],
            totalPrice: prev.totalPrice + item.PRODUCT_PRICE*(100-item.DISCOUNT)/100*item.QUANTITY,
          };
        } else
          return {
            products: [...prev.products],
            totalPrice: prev.totalPrice,
          };
      },
      {
        products: [],
        totalPrice: 0,
      }
    );

    if (UserCart.find((product) => product.checked === true)) {
      setNextable(true);
    } else {
      setNextable(false);
    }
    setTotalProductInfo(newArray);
  }, [UserCart]);

  const handleProductQuantity = (id, action, e) => {
    const newProductArray = UserCart.map((item) => {
      if (item.PRODUCT_ID === id) {
        if (action === "") {
          const { value } = e.target;
          item.QUANTITY = value;
        } else {
          action === "add" ? (item.QUANTITY += 1) : (item.QUANTITY -= 1);
        }
      }
      return item;
    });

    setUserCart(newProductArray);
    setCookies("cart", newProductArray);
  };

  return (
    <div className="section_container">
      <div className={styles.top_nav}>
        <div className="grid wide">
          <div className="row">
            <div className="col c-12">
              <div className={styles.top_nav_left_container}>
                <Link to="/">
                  <div className={styles.top_nav_item}>Trang ch</div>
                  <div
                    className={`${styles.top_nav_item} ${styles.item_separator}`}
                  >
                    Gi hng
                    <i className="fa-solid fa-cart-shopping" />
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="grid wide">
        <div className="row">
          <div className="col c-12">
            <div className={styles.container}>
              <div className={`${styles.header} ${styles.wrapper}`}>
                <div className={styles.checkbox_wrapper}>
                  <input
                    type="checkbox"
                    name="allSelect"
                    className={styles.checkbox}
                    checked={!UserCart.some((item) => item?.checked !== true)}
                    onChange={handleCheck}
                  />
                </div>
                <div className={styles.item_title}>Sn phm</div>
                <div className={styles.item_price_title}>n gi</div>
                <div className={styles.item_quantity_title}>S lng</div>
                <div className={styles.item_total_title}>S tin</div>
                <div className={styles.item_action_title}>Thao tc</div>
              </div>

              <div className={`${styles.wrapper} ${styles.body}`}>
                <CartItems
                  products={UserCart}
                  handleProductQuantity={handleProductQuantity}
                  handleCheck={handleCheck}
                  handleDeleteProduct={handleDeleteProduct}
                />
              </div>

              <div className={`${styles.footer} ${styles.wrapper}`}>
                <div className={styles.checkbox_wrapper}>
                  <input
                    type="checkbox"
                    name="allSelect"
                    id="footer_submit"
                    className={styles.checkbox}
                    checked={!UserCart.some((item) => item?.checked !== true)}
                    onChange={handleCheck}
                  />
                </div>
                <button
                  type="button"
                  className={styles.footer_checkall_button}
                  onClick={() =>
                    document.querySelector("#footer_submit").click()
                  }
                >
                  Chn tt c (<span>{UserCart.length}</span>)
                </button>
                <button
                  type="button"
                  className={styles.footer_delete_selected_button}
                >
                  Xo
                </button>
                <div className={styles.footer_total_product_info}>
                  Tng thanh ton (
                  <span className={styles.footer_quantity}>
                    {TotalProductInfo.products?.length}
                  </span>{" "}
                  Sn phm):
                  <span className={styles.footer_total_price}>
                    {MoneyShow({price: TotalProductInfo.totalPrice})}
                  </span>
                </div>
                <Link
                  to="/checkout"
                  className={`btn btn--primary ${
                    nextable ? null : "disabled"
                  } ${styles.footer_submit_button}`}
                >
                  Mua hng
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Cart;
