import styles from "./Profile.module.css";

import { useAuthDispatch, logout } from "../../context";
import { useCookies } from "react-cookie";

const RoleRender = (role) => {
  switch (role) {
    case 0:
      return "Qun tr h thng";
    case 1:
      return "Qun tr ca hng";
    case 2:
      return "Khch hng";
    default:
      return "Khng xc nh";
  }
};

function Profile({ isHide, user }) {
  const dispatch = useAuthDispatch();
  const [cookies, setCookies, removeCookie] = useCookies(["user", "cart"]);

  return (
    user? (
      <div className={`${styles.content} ${isHide ? styles.hide : ""}`}>
        <img src={user.USER_IMAGE} alt="" className={styles.img} />
        <span className={styles.details}>
          <div className={styles.name}>{user.USER_NAME}</div>
          <div className={styles.role}>{RoleRender(user.USER_ROLE)}</div>
        </span>
        <div className={styles.controller}>
          <i
            className="fa-solid fa-arrow-right-from-bracket"
            onClick={() => {
              logout(dispatch);
              removeCookie("user");
              removeCookie("cart");
            }}
          />
        </div>
      </div>
    ):null
  );
}

export default Profile;
