import { useState, useContext, useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'

import NavigationSearch from '../NavigationSearch/NavigationSearch'
import NavigationCartList from '../NavigationCart/NavigationCartList'
import UserNavigation from '../UserNavigation/UserNavigation'

function Navigation() {
    const [notifications, setNotifications] = useState([])

    const usePathname = () => {
        const location = useLocation()
        return location.pathname
    }

    return (
        <>
            <nav className="nav background-gradient-color">
                <div className="grid wide">
                    <div className="row">
                        <div className="col c-12">
                            <div className="nav-top d-flex flex-sb">
                                <div className="nav-item-container">
                                    <ul>
                                        <li>
                                            <a href="/" className="nav-item nav-link">Knh ngi bn</a>
                                        </li>
                                        <li>
                                            <a href="/" className="nav-item nav-link nav-item-separate">Tr thnh ngi bn</a>
                                        </li>
                                        <li className="has-dropdown">
                                            <a href="/" className="nav-item nav-link nav-item-separate">Ti ng dng</a>
                                            <div className="dropdown-pane tab-pane__qr">
                                                <img className="tab-pane__qr-img" src={process.env.PUBLIC_URL + "/resources/img/qrcode.png"} alt="qr-code" />
                                                <div className="sub-img-container">
                                                    <img src={process.env.PUBLIC_URL + "/resources/img/chplay.png"} alt="chplay" />
                                                    <img src={process.env.PUBLIC_URL + "/resources/img/apple.png"} alt="apple" />
                                                    <img src={process.env.PUBLIC_URL + "/resources/img/appgalery.png"} alt="appgalery" />
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <span href="/" className="nav-item nav-item-separate">
                                                Kt ni
                                                <a href="https://facebook.com/quocan.6302" className="fa-brands fa-facebook nav-link nav-brand-icon"></a>
                                                <a href="https://facebook.com/quocan.6302" className="fa-brands fa-instagram nav-link nav-brand-icon"></a>
                                            </span>
                                        </li>
                                    </ul>
                                </div>

                                <div className="nav-item-container">
                                    <ul>
                                        <li className="has-dropdown">
                                            <a href="/" className="nav-item nav-link">
                                                <i className="fa-solid fa-bell nav-link-icon"></i>
                                                Thng bo
                                            </a>
                                            <div className="dropdown-pane tab-pane__notification">
                                                <div className="notification__content">
                                                    {notifications === [] ?
                                                        "Hello" :
                                                        <div className="notification__content--empty">
                                                            <img src={process.env.PUBLIC_URL + "/resources/img/empty.png"} alt="Empty" className='notification__empty-img' />
                                                            <span className="notification__empty-content">ng nhp  xem thng bo</span>
                                                        </div>
                                                    }
                                                </div>
                                                <div className="notification__control">
                                                    <Link to="signup" className="notification__control-button">
                                                        ng k
                                                    </Link>
                                                    <Link to="login" className="notification__control-button">
                                                        ng nhp
                                                    </Link>
                                                </div>
                                            </div>
                                        </li>
                                        <li>
                                            <a href="/" className="nav-item nav-link">
                                                <i className="fa-solid fa-circle-question nav-link-icon"></i>
                                                H tr
                                            </a>
                                        </li>
                                        <UserNavigation />
                                    </ul>
                                </div>
                            </div>
                            {
                                !['/cart', '/payment'].includes(usePathname()) ?
                                    <div className="nav-search">
                                        <div className="nav-container">
                                            <Link to="/" className="nav-logo-link">
                                                <img src={process.env.PUBLIC_URL + "/resources/img/logo/logo-primary.png"} alt="" className="nav-logo" />
                                            </Link>
                                            <NavigationSearch />
                                            <NavigationCartList />
                                        </div>
                                    </div> :
                                    ''
                            }
                        </div>
                    </div>
                </div>
            </nav>
        </>
    )
}

export default Navigation