import React from "react";

import { render } from 'react-dom'
import { useState, useEffect } from "react";
import axios from "axios";
import { set, useForm } from "react-hook-form";
import styles from "./AdminCategory.module.css";

import MessagePopup from '../../components/Popup/Popup'
import ItemStorage from '../../components/ItemStorage/ItemStorage'

function AdminCategory() {
  const [Categories, setCategories] = useState([]);
  const [DeletedCategories, setDeletedCategories] = useState([]);
  const [Modal, setModal] = useState({ isOpen: false, type: "edit" });
  const [Popup, setPopup] = useState({isOpen: false, type: "", message: ""})
  const [SelectedCategory, setSelectedCategory] = useState({});
  const [Storage, setStorage] = useState(false)
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    axios("http://localhost/server/categories.php?GETALL=1")
      .then((response) => {
        const Categories = response.data;
        setCategories(Categories.filter(Category => Category.IS_DISABLED === '0'))
        setDeletedCategories(Categories.filter(Category => Category.IS_DISABLED !== '0'))
      })
      .catch((error) => console.log(error));
  }, []);

  const AddCategory = (data) => {
    axios({
      method: "POST",
      url: "http://localhost/server/add-category.php",
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
    .then((response) => {
      const result = response.data;
      if(result.Status === "OK") {
        alert(result.Message);
        setCategories(prev => [...prev, data])
        setModal({
          isOpen: false,
          type: ""
        })
      } else {
        alert(result.Message);
      }
    })
  };

  const UpdateCategory = (data) => {
    data.CATEGORY_ID = SelectedCategory.CATEGORY_ID
    axios({
      method: "POST",
      url: "http://localhost/server/update-category.php",
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
    .then((response) => {
      const result = response.data;
      if(result.Status === "OK") {
        alert(result.Message);
        setCategories(Categories.map(Category => Category.CATEGORY_ID===data.CATEGORY_ID?data:Category))
        setModal({
          isOpen: false,
          type: ""
        })
      } else {
        alert(result.Message);
      }
    })
  }

  const DeleteCategory = (CATEGORY_ID, STATUS) => {
    const text = `n danh mc ${CATEGORY_ID}`
    if(window.confirm(text) === true) {
      axios({
        method: "POST",
        url: "http://localhost/server/delete-category.php",
        data: JSON.stringify({"CATEGORY_ID": CATEGORY_ID, "STATUS": STATUS}),
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then((response) => {
          const result = response.data;
          if(result.Status === "OK") {
            setCategories(prev => prev.filter(Category => Category.CATEGORY_ID !== CATEGORY_ID))
            setDeletedCategories(prev => [...prev, Categories.filter(Category => Category.CATEGORY_ID === CATEGORY_ID)[0]])
          }
        })
    }
  } 

  const CancelDeleteItem = (CATEGORY_ID) => {
    axios({
      method: "POST",
      url: "http://localhost/server/delete-category.php",
      data: JSON.stringify({"CATEGORY_ID": CATEGORY_ID, "STATUS": "0"}),
      headers: {
        "Content-Type": "application/json"
      }
    })
      .then((response) => {
        const result = response.data;
        if(result.Status === "OK") {
          setCategories(prev => [...prev, DeletedCategories.filter(Category => Category.CATEGORY_ID === CATEGORY_ID)[0]])
          setDeletedCategories(prev => prev.filter(Category => Category.CATEGORY_ID !== CATEGORY_ID))
        }
      })
  }

  useEffect(() => {
    reset({
      keepValues: false
    })
  }, [Modal])

  const handleOpenModal = (type, Category_id) => {
    setModal({
      isOpen: true,
      type: type,
    });
    setSelectedCategory(
      Categories.filter((Category) => Category.CATEGORY_ID === Category_id)[0]
    );
  };

  const handleCloseModal = () => {
    setModal({
      isOpen: false,
      type: "",
    });
  };

  const handleOpenStorage = () => {
    setStorage(true)
  }

  const handleCloseStorage = () => {
    setStorage(false)
  }

  return (
    <>
      <div className={styles.section_container}>
        <div className={styles.adding}>
          <button
            className="btn btn--primary"
            onClick={() => handleOpenModal("add")}
          >
            Thm danh mc
          </button>
          <button className={`btn btn ${styles.storage_button}`} onClick={() => handleOpenStorage()}>
            Danh mc  n
          </button>
        </div>
        <div className={styles.content_container}>
          <table className={styles.category_table}>
            <thead>
              <tr>
                <th>#</th>
                <th>Tn danh mc</th>
                <th style={{ textAlign: "center" }}>Qun l</th>
              </tr>
            </thead>
            <tbody>
              {Categories &&
                Categories.map((Category) => (
                  <tr key={Category.CATEGORY_ID}>
                    <td>{Category.CATEGORY_ID}</td>
                    <td>{Category.CATEGORY_NAME}</td>
                    <td className={styles.category_control_container}>
                      <span
                        className={`${styles.category_control_button} ${styles.edit}`}
                        onClick={() =>
                          handleOpenModal("edit", Category.CATEGORY_ID)
                        }
                      >
                        Sa
                      </span>
                      <span
                        className={`${styles.category_control_button} ${styles.delete}`}
                        onClick={() => DeleteCategory(Category.CATEGORY_ID, 1)}
                      >
                        n
                      </span>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
      </div>

      {Modal.isOpen && (
        <div className={styles.modal}>
          <div className={styles.modal_container}>
            <form onSubmit={handleSubmit(Modal.type==="edit"?UpdateCategory:AddCategory)}>
              <div className={styles.modal_header}>
                <div className={styles.hero_title}>
                  {Modal.type === "edit" ? "Sa danh mc" : "Thm danh mc"}
                </div>
              </div>
              <div className={styles.modal_body}>
                <div className={styles.text_area}>
                  <div className={styles.label}>M danh mc</div>
                  {Modal.type === "edit" ? (
                    <div className={styles.input}>{SelectedCategory.CATEGORY_ID}</div>
                  ) : (
                    <input
                      type="text"
                      className={styles.input}
                      {...register("CATEGORY_ID")}
                    />
                  )}
                </div>
                <div className={styles.text_area}>
                  <div className={styles.label}>Tn danh mc</div>
                  {Modal.type === "edit" ? (
                    <input
                      type="text"
                      className={styles.input}
                      defaultValue={SelectedCategory.CATEGORY_NAME}
                      {...register("CATEGORY_NAME")}
                    />
                  ) : (
                    <input
                      type="text"
                      className={styles.input}
                      {...register("CATEGORY_NAME")}
                    />
                  )}
                </div>
              </div>
              <div className={styles.modal_controller}>
                <button type="button" className="btn" onClick={() => handleCloseModal()}>
                  Hu
                </button>
                <button className="btn btn--primary">Lu</button>
              </div>
            </form>
          </div>
        </div>
      )}

      {
        Popup.isOpen && <MessagePopup type={Popup.type} message={Popup.message} />
      }

      {
        Storage && <ItemStorage reference={"CATEGORY"} Items={DeletedCategories} handleCloseStorage={handleCloseStorage} CancelDeleteItem={CancelDeleteItem}/>
      }
    </>
  );
}

export default AdminCategory;
