import React from "react";
import { useState, useEffect } from "react";
import { useOutletContext, Link, useLocation } from "react-router-dom";
import axios from "axios";
import styles from "./UserInvoices.module.css";
import MoneyShow from "../MoneyShow/MoneyShow";
import InvoiceTypeFilter from "../InvoiceTypeFilter/InvoiceTypeFilter";

const OrderStatusRender = ({ ORDER_STATUS }) => {
  let TYPE = "";
  let CONTENT = "";

  ORDER_STATUS = Number.parseInt(ORDER_STATUS);

  switch (ORDER_STATUS) {
    case 2:
      TYPE = styles.completed;
      CONTENT = " giao";
      break;
    case 1:
      TYPE = styles.delivering;
      CONTENT = "ang giao";
      break;
    case 0:
      TYPE = styles.pending;
      CONTENT = "Ch x l";
      break;
    case 3:
      TYPE = styles.canceled;
      CONTENT = " hu";
      break;
    default:
      return;
  }

  return <div className={`${styles.invoice_status} ${TYPE}`}>{CONTENT}</div>;
};

const Invoice = ({ data }) => {
  return (
    <div className={styles.invoice}>
      <div className={styles.header}>
        <div className={styles.title}>{data.ORDER_CREATED_AT_FORMATED} M n hng: {data.ORDER_ID}</div>
        <OrderStatusRender ORDER_STATUS={data.ORDER_STATUS} />
      </div>
      <div className={styles.body}>
        <div className={styles.products_container}>
          {data.PRODUCTS.map((Product) => (
            <Link
              to={`/product/${Product.PRODUCT_ID}`}
              key={Product.PRODUCT_ID}
              className={styles.product_link}
            >
              <div className={styles.product}>
                <div className={styles.product_information_part}>
                  <div
                    className={styles.product_image}
                    style={{
                      backgroundImage: `url(${Product.PRODUCT_IMAGE})`,
                      backgroundSize: "contain",
                      backgroundRepeat: "no-repeat",
                    }}
                  />
                  <div className={styles.product_informations}>
                    <div className={styles.product_name}>
                      {Product.PRODUCT_NAME}
                    </div>
                    <div className={styles.product_name}>
                      x{Product.PRODUCT_QUANTITY}
                    </div>
                  </div>
                </div>
                <div className={styles.product_information_part}>
                  <div className={styles.product_price}>
                    {MoneyShow({
                      price: Product.PRODUCT_PRICE,
                      discount: Product.DISCOUNT,
                      quantity: Product.PRODUCT_QUANTITY,
                    })}
                  </div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
      <div className={styles.horizontal} />
      <div className={styles.invoice_grand_total}>
        <div className={styles.shipping_information}>
          <div className={styles.title}>a ch nhn hng</div>
          <div className={styles.info_container}>
            <span>{data.CUSTOMER_FULL_NAME}</span> <span>({data.CUSTOMER_PHONE_NUMBER})</span>
            <div>{data.CUSTOMER_ADDRESS}</div>
          </div>
        </div>
        <div className={styles.total_area}>
          Tng s tin:
          <span className={styles.value}>
            {MoneyShow({ price: data.ORDER_GRAND_TOTAL })}
          </span>
        </div>
      </div>
    </div>
  );
};

function UserInvoices() {
  const [User, setUser] = useOutletContext();
  const [Invoices, setInvoices] = useState();
  const location = useLocation();

  useEffect(() => {
    if (User.USER_ID) {
      axios(
        `http://localhost/server/user-invoices.php${
          location.search || "?status=all"
        }&USER_ID=${User.USER_ID}`
      ).then((response) => {
        window.scrollTo(0, 0);
        const result = response.data;
        if (result.SUCCESS) {
          setInvoices(result.INVOICES);
        }
      });
    }
  }, [User, location]);

  return (
    <div className={styles.container}>
      <div className={styles.header_container}>
        <InvoiceTypeFilter type="all" />
      </div>
      <div className={styles.body_container}>
        {Invoices &&
          Invoices.map((InvoiceItem) => (
            <Invoice key={InvoiceItem.ORDER_ID} data={InvoiceItem} />
          ))}
        {Invoices && Invoices.length === 0 && (
          <div className={styles.empty_invoice_list_container}>
            <div className={styles.empty_invoice_list}>
              <img
                src={process.env.PUBLIC_URL + "/resources/img/empty_invoice_list.png"}
                alt="empty_invoice_list"
                className={styles.image}
              />
              <div className={styles.title}>Cha c n hng</div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default UserInvoices;
