import { useState } from "react";
import styles from "./SearchFilter.module.css";


const FilterItemRender = ({
  Title,
  Type,
  Categories,
  handleChangeCheckbox
}) => {
  return (
    <div className={styles.filter_item}>
      <div className={styles.title}>{Title}</div>
      <div className={styles.category_container}>
        {Categories.map((Category) => (
          <div className={styles.wrapper} key={Category.ID}>
            <div
              className={styles.input_area}
              onClick={() => {
                document.querySelector(`input[name='${Category.ID}']`).click();
              }}
            >
              <input
                type="checkbox"
                name={Category.ID}
                onChange={() => handleChangeCheckbox(Type, Category.ID)}
              />
              <label htmlFor={Category.ID}>{Category.NAME}</label>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

const StarFilter = ({ CurrentRating, handleChangeRating }) => {
  const starSolid = (index) => (
    <i key={index} className={`fa-solid fa-star ${styles.star}`} />
  );
  const starThin = (index) => (
    <i key={index} className={`fa-regular fa-star ${styles.star}`} />
  );

  let StarsArray = [];

  for (let i = 1; i <= 5; i++) {
    let count = 0;
    let Stars = [];

    while (count !== i) {
      Stars.push(starSolid((i - 1) * 5 + count));
      count++;
    }

    while (count !== 5) {
      Stars.push(starThin((i - 1) * 5 + count));
      count++;
    }

    if (i !== 5) {
      Stars.push(
        <span key={"Title" + i} className={styles.title}>
          tr ln
        </span>
      );
    }

    StarsArray.push(
      <div
        key={i}
        className={`${styles.star_container} ${CurrentRating===i?styles.current:null}`}
        onClick={() => handleChangeRating(i)}
      >
        {Stars}
      </div>
    );
  }

  return (
    <div className={styles.filter_item}>
      <div className={styles.title}>nh gi</div>
      <div className={styles.category_container}>{StarsArray.reverse()}</div>
    </div>
  );
};

function SearchFilter({
  Filters: FilterItems,
  handleChangeCurrentFilter,
  setFilterPrice,
  handleChangeCheckbox: changeFilter,
  handleClearFilter
}) {
  const [Filters, setFilters] = useState([]);
  const [CurrentRating, setCurrentRating] = useState(0)

  const handleChangeCheckbox = (Type, ID) => {
    let newFilters = [];
    if (Filters.find((Item) => Item.Type === Type)) {
      newFilters = Filters.map((Filter) => {
        if (Filter.Type === Type) {
          if (Filter.Checked.find((ItemID) => ItemID === ID)) {
            Filter.Checked = Filter.Checked.filter((ItemID) => ItemID !== ID);
          } else {
            Filter.Checked = [...Filter.Checked, ID];
          }
        }

        return Filter.Checked?.length > 0 ? Filter : null;
      });
    } else {
      newFilters = [...Filters, { Type: Type, Checked: [ID] }];
    }
    newFilters = newFilters.filter((Filter) => Filter !== null);
    setFilters(newFilters);
    changeFilter(newFilters);
  };

  const handleChangeRating = (Rating) => {
    setCurrentRating(prevRating => prevRating===Rating?0:Rating)
    handleChangeCurrentFilter("rating", Rating)
  }

  const handleClearAllChoices = () => {
    setCurrentRating(0)
    setFilters([])
    document.querySelectorAll("input[type='checkbox']").forEach(Input => {
      Input.checked = false
    })
    handleClearFilter()
  }

  return (
    <div className={styles.container}>
      <div className={styles.header_container}>
        <i className={`fa-solid fa-filter ${styles.icon}`} />
        <span className={styles.title}>B lc tm kim</span>
      </div>
      <div className={styles.body_container}>
        <div className={styles.filter_item}>
          <div className={styles.title}>Khong gi</div>
          <div className={styles.price_filter}>
            <div className={styles.price_filter_box}>
              <input
                type="number"
                className={styles.input_box}
                placeholder="T"
                name="minPrice"
              />
              <div className={styles.horizontal}></div>
              <input
                type="number"
                className={styles.input_box}
                placeholder="n"
                name="maxPrice"
              />
            </div>
            <div className={styles.price_filter_controller}>
              <button
                className="btn btn--primary"
                onClick={() => setFilterPrice()}
              >
                p dng
              </button>
            </div>
          </div>
        </div>
        {StarFilter({ CurrentRating , handleChangeRating })}
        {FilterItems.map((Item, index) => (
          <FilterItemRender
            key={index}
            Title={Item.Title}
            Type={Item.Type}
            Categories={Item.Categories}
            handleChangeCheckbox={handleChangeCheckbox}
          />
        ))}
        <div className={styles.filter_item}>
            <button type="button" className="btn btn--primary" onClick={() => handleClearAllChoices()}>Xo b lc</button>
        </div>
      </div>
    </div>
  );
}

export default SearchFilter;
