import React from "react";

import { Link } from "react-router-dom";

import styles from "./Widget.module.css";
import MoneyShow from "../MoneyShow/MoneyShow"

function Widget({ type, value }) {
  let data;

  switch (type) {
    case "earnings":
      data = {
        title: "Doanh thu",
        subTitle: "Chi tit doanh thu",
        isMoney: true,
        icon: "fa-solid fa-money-bill",
        link: "/admin/orders",
        color: "rgb(62 112 53)",
      };
      break;
    case "users":
      data = {
        title: "Ngi dng",
        subTitle: "Xem tt c ngi dng",
        isMoney: false,
        icon: "fa-solid fa-user",
        link: "/admin/orders",
        color: "rgb(84 88 219)",
      };
      break;
    case "orders":
      data = {
        title: "n hng mi",
        subTitle: "Xem tt c n hng",
        isMoney: false,
        icon: "fa-solid fa-cart-shopping",
        link: "/admin/orders",
        color: "rgb(239 224 38)",
      };
      break;
    case "products":
      data = {
        title: "Sn phm",
        subTitle: "Xem tt c sn phm",
        isMoney: false,
        icon: "fa-solid fa-box",
        link: "/admin/shop",
        color: "rgb(229 171 9)",
      };
      break;
  }

  return (
    <div className={styles.container}>
      <div className={styles.title}>{data.title}</div>
      <div className={styles.counter}>
        {data.isMoney ? MoneyShow({price: value}) : value}
      </div>
      <div className={styles.bottom_container}>
        <Link to={data.link} className={styles.link}>
          {data.subTitle}
        </Link>
        <i
          className={`${data.icon} ${styles.icon}`}
          style={{ color: data.color }}
        />
      </div>
    </div>
  );
}

export default Widget;
