import { useState, useEffect, useCallback } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import axios from "axios";

import SideCategory from "../../components/Category/SideCategory";
import SideFilter from "../../components/Filter/SideFilter";
import Products from "../../components/Products";
import Loading from "../../components/Loading";
import Pagination from "../../components/Pagination/Pagination";

function Shop() {
  const [productList, setProductList] = useState([]);
  const [filters, setFilters] = useState({
    limit: 20,
    page: 1,
  });
  
  const [isLoading, setIsLoading] = useState(true);
  const [searchParams, setSearchParams] = useSearchParams();
  const location = useLocation();
  const [categoryList, setCategoryList] = useState([]);
  const [TotalPage, setTotalPage] = useState(1)

  useEffect(() => {
    setSearchParams(filters);
  }, [filters]);

  useEffect(() => {
    setIsLoading(true);
    window.scrollTo(0, 0)
    const getProducts = `http://localhost/server/product.php${location.search}`;
    const getCategories = "http://localhost/server/product-category.php";
    const requestProducts = axios.get(getProducts);
    const requestCategories = axios.get(getCategories);

    axios.all([requestProducts, requestCategories]).then(
      axios.spread((...response) => {
        setIsLoading(false);
        const Products = response[0];
        const Categories = response[1];
        setProductList(
          Products.data.PRODUCTS.filter(
            (Product) => Product.IS_DISABLED === "0"
          )
        );
        setTotalPage(Products.data.LENGTH);
        setCategoryList(
          Categories.data.filter((Category) => Category.IS_DISABLED === "0")
        );
      })
    );
  }, [location.search]);

  const handleChangeCurrentFilter = useCallback(
    (filterName, filterValue) => {
      setFilters((prev) => ({
        ...prev,
        page: 1,
        [filterName]: filterValue,
      }));
    },
    [filters]
  );

  return (
    <>
      <div className="content-container">
        <div className="grid wide">
          <div className="row">
            <div className="col c-2">
              <SideCategory
                categories={categoryList}
                handleChangeCurrentFilter={handleChangeCurrentFilter}
              />
            </div>
            <div className="col c-10">
              <SideFilter
                handleChangeCurrentFilter={handleChangeCurrentFilter}
              />
              <div className="view-item-container">
                {isLoading ? (
                  <Loading />
                ) : (
                  <Products products={productList} filters={filters} />
                )}
              </div>
              <Pagination
                productsPerPage={filters.limit}
                totalProductsLength={TotalPage}
                handleChangeCurrentFilter={handleChangeCurrentFilter}
                currentPage={filters.page}
                isLoading={isLoading}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Shop;
