import { Link } from "react-router-dom";
import style from "./product.module.css";
import MoneyShow from "../../components/MoneyShow/MoneyShow";

function Product({ props }) {
  const RatingStarRendering = (quantity) => {
    const StarContainer = [];
    const charge = Math.round(quantity - Number.parseInt(quantity));
    for (let i = 0; i < Math.floor(quantity); i++)
      StarContainer.push(
        <i
          key={i}
          className={`fa-solid fa-star ${style.item_rating_icon__active}`}
        ></i>
      );
    if (charge)
      StarContainer.push(
        <i
          key={charge + Math.floor(quantity)}
          className={`fa-solid fa-star-half-stroke ${style.item_rating_icon__active}`}
        ></i>
      );
    for (let i = 5; i > Math.floor(quantity) + charge; i--)
      StarContainer.push(<i key={i} className={`fa-regular fa-star`}></i>);

    return StarContainer;
  };

  return (
    <>
      <div className={style.item} key={props.PRODUCT_ID}>
        <Link to={`/product/${props.PRODUCT_ID}`} className={style.item_link}>
          <div className={style.item_view}>
            <div className={style.item_img_wrapper} style={{background: `url("${props.PRODUCT_IMAGE}")`, backgroundSize: "contain", backgroundRepeat: "no-repeat", backgroundPosition: "50%"}}>
              {/* <img
                src={props.PRODUCT_IMAGE}
                alt=""
                className={style.item_img}
              /> */}
            </div>
            <div className={style.item_info_wrapper}>
              <div className={style.item_info_name}>{props.PRODUCT_NAME}</div>
              <div className={style.item_info_price_wrapper}>
                {props.DISCOUNT !== "0" ? (
                  <>
                    <span
                      className={`${style.item_info_old_price} + ${style.item_info_price}`}
                    >
                      {MoneyShow({price: props.PRODUCT_PRICE})}
                    </span>
                    <span
                      className={`${style.item_info_current_price} + ${style.item_info_price}`}
                    >
                      {MoneyShow({price: props.PRODUCT_PRICE, discount: props.DISCOUNT})}
                    </span>
                  </>
                ) : (
                  <span
                    className={`${style.item_info_current_price} + ${style.item_info_price}`}
                  >
                    {MoneyShow({price: props.PRODUCT_PRICE})}
                  </span>
                )}
              </div>
              <div className={style.item_info_meta_wrapper}>
                <div className={style.item_info_meta_like}>
                  <i className="fa-regular fa-heart" />
                </div>
                <div className={style.item_info_meta_start_rating}>
                  {RatingStarRendering(props.RATING)}
                </div>
                <div className={style.item_info_meta_quantity_sold}>
                  S lng {props.STOCK}
                </div>
              </div>
              <div className={style.item_info_original}>
                <div className={style.item_info_manufacturer}>
                  {props.MANUFACTURER}
                </div>
                <div className={style.item_info_shipment_from}>TP HCM</div>
              </div>
            </div>
          </div>
        </Link>
        {props.DISCOUNT !== "0" && (
            <div className={style.badge}>
              <div className={style.badge_container}>
                <div className={style.value}>{props.DISCOUNT}%</div>
                <div className={style.content}>
                  Gim
                </div>
              </div>
            </div>
          )}
      </div>
    </>
  );
}

export default Product;
