import React from 'react'
import { Link, useLocation } from 'react-router-dom'
import styles from './InvoiceTypeFilter.module.css'

const Types = [
  {
    title: "Tt c",
    slug: "all",
  },
  {
    title: "Ch x l",
    slug: "0",
  },
  {
    title: "ang giao",
    slug: "1",
  },
  {
    title: " giao",
    slug: "2",
  },
  {
    title: " hu",
    slug: "3",
  }
]

function InvoiceTypeFilter() {
  const location = useLocation()

  return (
    <div className={styles.container}>
      {
        Types.map(Type => (
          <Link to={`/user/purchase?status=${Type.slug}`} className={`${styles.item_link} ${(location.search===`?status=${Type.slug}` || (location.search === "" && Type.slug === "all"))?styles.active:null}`} key={Type.slug}>
            {Type.title}
          </Link>
        ))
      }
    </div>
  )
}

export default InvoiceTypeFilter