import { useState, useEffect } from "react";
import styles from "./UserProfile.module.css";
import { useForm } from "react-hook-form";
import { useOutletContext } from "react-router-dom";
import { useCookies } from 'react-cookie'
import axios from "axios";

const inputBox = document.querySelectorAll(`.${styles.input_box}`);
inputBox.forEach((input) => {
  input.addEventListener("focus", (e) => {
    const inputWrapper = e.target.closest(`.${styles.input_wrapper}`);
    inputWrapper.classList.add(styles.focus);
  });
  input.addEventListener("blur", (e) => {
    const inputWrapper = e.target.closest(`.${styles.input_wrapper}`);
    inputWrapper.classList.remove(styles.focus);
  });
});

function dateFormat(inputDate, format) {
  //parse the input date
  const date = new Date(inputDate);

  //extract the parts of the date
  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear();

  //replace the month
  format = format.replace("MM", month.toString().padStart(2, "0"));

  //replace the year
  if (format.indexOf("yyyy") > -1) {
    format = format.replace("yyyy", year.toString());
  } else if (format.indexOf("yy") > -1) {
    format = format.replace("yy", year.toString().substr(2, 2));
  }

  //replace the day
  format = format.replace("dd", day.toString().padStart(2, "0"));

  return format;
}

function UserProfile() {
  const [User, setUser] = useOutletContext();
  const [cookies, setCookie] = useCookies();
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm({ mode: "onBlur" });
  const [Avatar, setAvatar] = useState({});

  const handleChangeAvatar = (event) => {
    let file = event.target.files[0];
    if (file) {
      const name = URL.createObjectURL(file);
      setAvatar({
        name: name,
        file: file,
      });
    }
  };

  useEffect(() => {
    if (User) {
      setValue("USER_FULL_NAME", User.USER_FULL_NAME);
      setValue("USER_EMAIL", User.USER_EMAIL);
      setValue("USER_PHONE_NUMBER", User.USER_PHONE_NUMBER);
      setValue("USER_GENDER", User.USER_GENDER);
      setValue("USER_ADDRESS", User.USER_ADDRESS);
      setValue("USER_DOB", User.USER_DOB);
    }
  }, [User]);

  useEffect(() => {
    return () => {
      URL.revokeObjectURL(Avatar.name);
    };
  }, [Avatar]);

  const updateUserAvatar = (USER_ID) => {
    const formData = new FormData();
    formData.append("image", Avatar.file);
    formData.append("USER_ID", JSON.stringify({ USER_ID: USER_ID }));
    axios({
      method: "POST",
      url: `http://localhost/server/update-user.php?USER_ID=${USER_ID}`,
      data: formData,
      header: {
        "Content-Type": "multipart/form-data",
      },
    }).then((response) => {
      const result = response.data;
      if (!result.SUCCESS) {
        alert(result.MESSAGE)
      }
    });
  };

  const handleSaveUserData = (data) => {
    data.USER_ID = User.USER_ID;
    data.USER_DOB = dateFormat(
      data.USER_DOB.toLocaleDateString(),
      "yyyy-MM-dd"
    );

    axios({
      method: "POST",
      url: "http://localhost/server/update-user.php",
      data: data,
    }).then((response) => {
      const result = response.data;
      if (result.SUCCESS) {
        Avatar.file && updateUserAvatar(User.USER_ID);

        const newUser = {...cookies.user}
        Object.keys(data).forEach(key => {
          newUser[key] = data[key];
        })
        setCookie("user", {...newUser}, {path: "/"})
        alert(result.MESSAGE)
      }
    });


  };

  return (
    <div className={styles.container}>
      <div className={styles.header_container}>
        <div className={styles.hero_title}>H s ngi dng</div>
      </div>
      <div className={styles.horizontal}></div>
      <div className={styles.body_container}>
        <form
          onSubmit={handleSubmit(handleSaveUserData)}
          className={styles.user_information_form}
        >
          <div className="row">
            <div className="col c-8">
              <div className={styles.text_area}>
                <div className={styles.input_label}>Tn ng nhp</div>
                <div className={styles.input_wrapper}>
                  <div type="text" className={styles.text_content}>
                    {User.USER_NAME}
                  </div>
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>H v tn</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="text"
                    className={styles.input_box}
                    defaultValue={User.USER_FULL_NAME}
                    {...register("USER_FULL_NAME", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>S in thoi</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="text"
                    className={styles.input_box}
                    defaultValue={User.USER_PHONE_NUMBER}
                    {...register("USER_PHONE_NUMBER", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      pattern: {
                        value: /(((\+|)84)|0)(3|5|7|8|9)+([0-9]{8})\b/,
                        message: "khng hp l",
                      },
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>a ch</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="text"
                    className={styles.input_box}
                    defaultValue={User.USER_ADDRESS}
                    {...register("USER_ADDRESS", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      minLength: {
                        value: 10,
                        message: "phi nhiu hn 10 k t",
                      },
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>Email</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="text"
                    className={styles.input_box}
                    defaultValue={User.USER_EMAIL}
                    {...register("USER_EMAIL", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      pattern: {
                        value: /^[\w.+\-]+@gmail\.com$/,
                        message: "khng hp l",
                      },
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>Gii tnh</div>
                <div className={styles.input_wrapper}>
                  <select
                    className={styles.select_box}
                    defaultValue={User.USER_GENDER}
                    {...register("USER_GENDER")}
                  >
                    <option value="0">Nam</option>
                    <option value="1">N</option>
                  </select>
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>Ngy sinh</div>
                <div className={styles.input_wrapper}>
                  <input
                    type="date"
                    className={styles.input_date_box}
                    defaultValue={User.USER_DOB}
                    {...register("USER_DOB", {
                      valueAsDate: true,
                    })}
                  />
                </div>
              </div>
            </div>
            <div className="col c-4">
              <div className={styles.avatar_preview_area}>
                <div
                  className={styles.avatar_preview_box}
                  style={{
                    backgroundImage: `url(${Avatar.name || User.USER_IMAGE})`,
                    backgroundSize: "contain",
                    backgroundRepeat: "no-repeat",
                  }}
                />
                <input
                  type="file"
                  className={styles.avatar_input}
                  onChange={(e) => handleChangeAvatar(e)}
                />
                <button
                  type="button"
                  className="btn"
                  onClick={() => {
                    document.querySelector(`.${styles.avatar_input}`).click();
                  }}
                >
                  Chn nh
                </button>
              </div>
            </div>
          </div>
          {Object.keys(errors).length !== 0 && console.log(errors)}
          <div className={styles.form_controller}>
            <button className="btn btn--primary">Lu</button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default UserProfile;
