import { useState } from 'react'
import { Link } from 'react-router-dom'
import styles from './UserNavigation.module.css'
import { useCookies } from 'react-cookie'

function UserNavigation() {
  const [cookies, setCookie, removeCookie] = useCookies(['user']);
  const [isShown, setIsShown] = useState(false)
  const userDetail  = cookies.user

  const handleLogout = () => {
    removeCookie("user", {path: "/"});
  }

  return (
    userDetail?
      <li>
        <div
          className={`nav-item ${styles.container}`}
          onMouseEnter={() => setIsShown(true)}
          onMouseLeave={() => setIsShown(false)}
        >
          <div className={styles.avatar}>
            <img src={userDetail.USER_IMAGE || '/resources/img/1024px-User-avatar.svg.png'} alt="" className={styles.avatar_img} />
          </div>
          <div className={styles.name_wrapper}>
            <span className={styles.name}>{`${userDetail.USER_NAME}`}</span>
          </div>
          {isShown && (
            <div className={styles.dropdown}>
              <ul className={styles.nav_list}>
                {
                  [0, 1].includes(userDetail.USER_ROLE) ?
                    <li>
                      <Link to="/admin" className={styles.nav_item}>i n trang qun tr</Link>
                    </li> : null
                }
                <li>
                  <Link to="/user/profile" className={styles.nav_item}>Ti khon ca ti</Link>
                </li>
                <li>
                  <Link to="/user/purchase" className={styles.nav_item}>n mua</Link>
                </li>
                <li>
                  <div className={styles.nav_item} onClick={() => handleLogout()}>ng xut</div>
                </li>
              </ul>
            </div>
          )}
        </div>
      </li>
      :
      <>
        <li>
          <Link to="/signup" className="nav-item nav-link">ng k</Link>
        </li>
        <li>
          <Link to="/login" className="nav-item nav-link nav-item-separate">ng nhp</Link>
        </li>
      </>
  )
}

export default UserNavigation

