import React from "react";
import { useForm } from "react-hook-form";
import { useOutletContext } from "react-router-dom";
import axios from 'axios'
import styles from "./ChangePassword.module.css";

axios.defaults.withCredentials = true;

const inputBox = document.querySelectorAll(`.${styles.input_box}`);
inputBox.forEach((input) => {
  input.addEventListener("focus", (e) => {
    const inputWrapper = e.target.closest(`.${styles.input_wrapper}`);
    inputWrapper.classList.add(styles.focus);
  });
  input.addEventListener("blur", (e) => {
    const inputWrapper = e.target.closest(`.${styles.input_wrapper}`);
    inputWrapper.classList.remove(styles.focus);
  });
});

function ChangePassword() {
  const [User, setUser] = useOutletContext();
  const {
    register,
    handleSubmit,
    validate,
    reset,
    formState: { errors },
  } = useForm();

  const handleChangePassword = (data) => {
    data.USER_ID = User.USER_ID
    axios({
      method: 'POST',
      url: "http://localhost/server/changepassword.php",
      data: data,
      headers: { 'Content-Type': 'application/json'}
    })
    .then(response => {
      const result = response.data;
      if(result.SUCCESS) {
        reset({
          keepValues: false
        })
        alert(result.MESSAGE)
      } else {
        alert(result.MESSAGE)
      }
    })
  };

  return (
    <div className={styles.container}>
      <div className={styles.header_container}>
        <div className={styles.hero_title}>i mt khu</div>
      </div>
      <div className={styles.horizontal}></div>
      <div className={styles.body_container}>
        <form
          onSubmit={handleSubmit(handleChangePassword)}
          className={styles.changePassword_form}
        >
          <div className="row">
            <div className="col c-6">
              <div className={styles.text_area}>
                <div className={styles.input_label}>Mt khu c</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="password"
                    className={styles.input_box}
                    {...register("USER_PASSWORD", {
                      required: { value: true, message: "khng c b trng" },
                      minLength: {
                        value: 8,
                        message: "phi nhiu hn 8 k t",
                      },
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>Xc nhn mt khu c</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="password"
                    className={styles.input_box}
                    {...register("USER_RETYPE_PASSWORD", {
                      required: { value: true, message: "khng c b trng" },
                      validate: (password) =>
                        password ===
                          document.querySelector("input[name='USER_PASSWORD']")
                            .value || "khng chnh xc",
                    })}
                  />
                </div>
              </div>
              <div className={styles.text_area}>
                <div className={styles.input_label}>Mt khu mi</div>
                <div
                  className={`${styles.input_wrapper} ${styles.have_input_box}`}
                >
                  <input
                    type="password"
                    className={styles.input_box}
                    {...register("USER_NEW_PASSWORD", {
                      required: {
                        value: true,
                        message: "khng c b trng",
                      },
                      minLength: {
                        value: 8,
                        message: "phi nhiu hn 8 k t",
                      },
                      validate: password => password !== document.querySelector("input[name='USER_PASSWORD']").value || 'khng c trng vi mt khu c'
                    })}
                  />
                </div>
              </div>
              <div className={styles.form_controller}>
                <button className="btn btn--primary">Xc nhn</button>
              </div>
            </div>
            <div className="col c-6">
              <div className={styles.errors_area}>
                {Object.keys(errors).length !== 0 && (
                  <ul className={styles.errors_container}>
                    {errors.USER_PASSWORD && <li className={styles.errors_item}>Mt khu: {errors.USER_PASSWORD.message}</li>}
                    {errors.USER_RETYPE_PASSWORD && <li className={styles.errors_item}>Nhp li mt khu: {errors.USER_RETYPE_PASSWORD.message}</li>}
                    {errors.USER_NEW_PASSWORD && <li className={styles.errors_item}>Mt khu mi: {errors.USER_NEW_PASSWORD.message}</li>}
                  </ul>
                )}
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default ChangePassword;
