import React from "react";

import { render } from 'react-dom'
import { useState, useEffect } from "react";
import axios from "axios";
import { set, useForm } from "react-hook-form";
import styles from "./AdminCategory.module.css";

import MessagePopup from '../../components/Popup/Popup'
import ItemStorage from '../../components/ItemStorage/ItemStorage'

function AdminManufacturer() {
  const [Manufacturers, setManufacturers] = useState([]);
  const [DeletedManufacturers, setDeletedManufacturers] = useState([]);
  const [Modal, setModal] = useState({ isOpen: false, type: "edit" });
  const [Popup, setPopup] = useState({isOpen: false, type: "", message: ""})
  const [SelectedManufacturer, setSelectedManufacturer] = useState({});
  const [Storage, setStorage] = useState(false)
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    axios("http://localhost/server/manufacturers.php")
      .then((response) => {
        const Manufacturers = response.data;
        setManufacturers(Manufacturers.filter(Category => Category.IS_DISABLED === '0'))
        setDeletedManufacturers(Manufacturers.filter(Category => Category.IS_DISABLED !== '0'))
      })
      .catch((error) => console.log(error));
  }, []);

  const AddCategory = (data) => {
    axios({
      method: "POST",
      url: "http://localhost/server/add-manufacturer.php",
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
    .then((response) => {
      const result = response.data;
      if(result.Status === "OK") {
        alert(result.Message);
        setManufacturers(prev => [...prev, data])
        setModal({
          isOpen: false,
          type: ""
        })
      } else {
        alert(result.Message);
      }
    })
  };

  const UpdateCategory = (data) => {
    data.MANUFACTURER_ID = SelectedManufacturer.MANUFACTURER_ID
    axios({
      method: "POST",
      url: "http://localhost/server/update-manufacturer.php",
      data: data,
      headers: {
        "Content-Type": "multipart/json",
      },
    })
    .then((response) => {
      const result = response.data;
      if(result.Status === "OK") {
        alert(result.Message);
        setManufacturers(Manufacturers.map(Category => Category.MANUFACTURER_ID===data.MANUFACTURER_ID?data:Category))
        setModal({
          isOpen: false,
          type: ""
        })
      } else {
        alert(result.Message);
      }
    })
  }

  const DeleteCategory = (MANUFACTURER_ID, STATUS) => {
    const text = `n nh phn phi ${MANUFACTURER_ID}`
    if(window.confirm(text) === true) {
      axios({
        method: "POST",
        url: "http://localhost/server/delete-manufacturer.php",
        data: JSON.stringify({"MANUFACTURER_ID": MANUFACTURER_ID, "STATUS": STATUS}),
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then((response) => {
          const result = response.data;
          if(result.Status === "OK") {
            setManufacturers(prev => prev.filter(Category => Category.MANUFACTURER_ID !== MANUFACTURER_ID))
            setDeletedManufacturers(prev => [...prev, Manufacturers.filter(Category => Category.MANUFACTURER_ID === MANUFACTURER_ID)[0]])
          }
        })
    }
  } 

  const CancelDeleteItem = (MANUFACTURER_ID) => {
    axios({
      method: "POST",
      url: "http://localhost/server/delete-manufacturer.php",
      data: JSON.stringify({"MANUFACTURER_ID": MANUFACTURER_ID, "STATUS": "0"}),
      headers: {
        "Content-Type": "application/json"
      }
    })
      .then((response) => {
        const result = response.data;
        if(result.Status === "OK") {
          setManufacturers(prev => [...prev, DeletedManufacturers.filter(Category => Category.MANUFACTURER_ID === MANUFACTURER_ID)[0]])
          setDeletedManufacturers(prev => prev.filter(Category => Category.MANUFACTURER_ID !== MANUFACTURER_ID))
        }
      })
  }

  useEffect(() => {
    reset({
      keepValues: false
    })
  }, [Modal])

  const handleOpenModal = (type, MANUFACTURER_ID) => {
    setModal({
      isOpen: true,
      type: type,
    });
    setSelectedManufacturer(
      Manufacturers.filter((Category) => Category.MANUFACTURER_ID === MANUFACTURER_ID)[0]
    );
  };

  const handleCloseModal = () => {
    setModal({
      isOpen: false,
      type: "",
    });
  };

  const handleOpenStorage = () => {
    setStorage(true)
  }

  const handleCloseStorage = () => {
    setStorage(false)
  }

  return (
    <>
      <div className={styles.section_container}>
        <div className={styles.adding}>
          <button
            className="btn btn--primary"
            onClick={() => handleOpenModal("add")}
          >
            Thm nh phn phi
          </button>
          <button className={`btn btn ${styles.storage_button}`} onClick={() => handleOpenStorage()}>
            Nh phn phi  n
          </button>
        </div>
        <div className={styles.content_container}>
          <table className={styles.category_table}>
            <thead>
              <tr>
                <th>#</th>
                <th>Tn nh phn phi</th>
                <th style={{ textAlign: "center" }}>Qun l</th>
              </tr>
            </thead>
            <tbody>
              {Manufacturers &&
                Manufacturers.map((Category) => (
                  <tr key={Category.MANUFACTURER_ID}>
                    <td>{Category.MANUFACTURER_ID}</td>
                    <td>{Category.MANUFACTURER_NAME}</td>
                    <td className={styles.category_control_container}>
                      <span
                        className={`${styles.category_control_button} ${styles.edit}`}
                        onClick={() =>
                          handleOpenModal("edit", Category.MANUFACTURER_ID)
                        }
                      >
                        Sa
                      </span>
                      <span
                        className={`${styles.category_control_button} ${styles.delete}`}
                        onClick={() => DeleteCategory(Category.MANUFACTURER_ID, 1)}
                      >
                        n
                      </span>
                    </td>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
      </div>

      {Modal.isOpen && (
        <div className={styles.modal}>
          <div className={styles.modal_container}>
            <form onSubmit={handleSubmit(Modal.type==="edit"?UpdateCategory:AddCategory)}>
              <div className={styles.modal_header}>
                <div className={styles.hero_title}>
                  {Modal.type === "edit" ? "Sa danh mc" : "Thm danh mc"}
                </div>
              </div>
              <div className={styles.modal_body}>
                <div className={styles.text_area}>
                  <div className={styles.label}>M nh phn phi</div>
                  {Modal.type === "edit" ? (
                    <div className={styles.input}>{SelectedManufacturer.MANUFACTURER_ID}</div>
                  ) : (
                    <input
                      type="text"
                      className={styles.input}
                      {...register("MANUFACTURER_ID")}
                    />
                  )}
                </div>
                <div className={styles.text_area}>
                  <div className={styles.label}>Tn nh phn phi</div>
                  {Modal.type === "edit" ? (
                    <input
                      type="text"
                      className={styles.input}
                      defaultValue={SelectedManufacturer.MANUFACTURER_NAME}
                      {...register("MANUFACTURER_NAME")}
                    />
                  ) : (
                    <input
                      type="text"
                      className={styles.input}
                      {...register("MANUFACTURER_NAME")}
                    />
                  )}
                </div>
              </div>
              <div className={styles.modal_controller}>
                <button type="button" className="btn" onClick={() => handleCloseModal()}>
                  Hu
                </button>
                <button className="btn btn--primary">Lu</button>
              </div>
            </form>
          </div>
        </div>
      )}

      {
        Popup.isOpen && <MessagePopup type={Popup.type} message={Popup.message} />
      }

      {
        Storage && <ItemStorage Reference={"MANUFACTURER"} Items={DeletedManufacturers} handleCloseStorage={handleCloseStorage} CancelDeleteItem={CancelDeleteItem}/>
      }
    </>
  );
}

export default AdminManufacturer;
