import { useEffect } from "react";
import { Routes, Route, useLocation } from "react-router-dom";
import { useCookies } from "react-cookie";
import Layout from "./resources/Layout";
import "./resources/css/base.css";
import "./resources/css/grid.css";
import "./resources/css/main.css";
import "./resources/css/slider.css";

import { AuthProvider, CartProvider } from "./resources/context";
import Home from "./resources/pages/Home/Home";
import Shop from "./resources/pages/Shop/Shop";
import LoginSite from "./resources/pages/Login/LoginSite";
import SignUpSite from "./resources/pages/SignUp/SignUpSite";
import ProductViewer from "./resources/pages/Product/ProductViewer";
import Cart from "./resources/components/Cart";
import Checkout from "./resources/pages/Checkout/Checkout";
import Search from "./resources/pages/Search/Search";
import User from "./resources/pages/User/User";
import UserProfile from "./resources/components/UserProfile/UserProfile";
import UserChangePassword from "./resources/components/ChangePassword/ChangePassword";
import UserInvoices from "./resources/components/UserInvoices/UserInvoices";
import Admin from "./resources/pages/Admin/Admin";
import AdminHome from "./resources/pages/AdminHome/Home";
import AdminShop from "./resources/pages/AdminShop/AdminShop";
import AdminUsers from "./resources/pages/AdminUsers/AdminUsers";
import AdminOrders from "./resources/pages/AdminOrders/AdminOrders";
import AdminCategory from "./resources/pages/AdminCategory/AdminCategory";
import AdminManufacturer from "./resources/pages/AdminManufacturer/AdminManufacturer";
import AdminProtected from "./resources/components/AdminProtected/AdminProtected";
function App() {
  const pathName = useLocation().pathname;

  useEffect(() => {
    document.body.style.background = pathName.match("/admin") && "#fff";
  }, [pathName]);

  const [cookies, setCookie] = useCookies();

  return (
    <AuthProvider>
      <CartProvider>
        <Routes>
          <Route path="/" element={<Layout />}>
            <Route index element={<Home />} />
          </Route>

          <Route path="products" element={<Layout></Layout>}>
            <Route index element={<Shop />}></Route>
          </Route>

          <Route
            path="user"
            element={
              <Layout>
                <User />
              </Layout>
            }
          >
            <Route path="profile" element={<UserProfile />} />
            <Route path="password" element={<UserChangePassword />} />
            <Route path="purchase" element={<UserInvoices />} />
          </Route>

          <Route path="product" element={<Layout />}>
            <Route path=":productID" element={<ProductViewer />} />
          </Route>

          <Route path="search" element={<Layout />}>
            <Route index element={<Search />} />
          </Route>

          <Route path="cart" element={<Layout />}>
            <Route index element={<Cart />} />
          </Route>

          <Route path="checkout" element={<Layout />}>
            <Route index element={<Checkout />} />
          </Route>

          <Route
            path="admin"
            element={
              <AdminProtected>
                <Admin />
              </AdminProtected>
            }
          >
            {
              cookies?.user?.USER_ROLE === 0?
              <>
                <Route index element={<AdminUsers />} />
              </>:
              <>
                <Route index element={<AdminHome />} />
                <Route path="shop" element={<AdminShop />} />
                <Route path="orders" element={<AdminOrders />} />
                <Route path="category" element={<AdminCategory />} />
                <Route path="manufacturers" element={<AdminManufacturer />} />
              </>
            }
          </Route>
          <Route path="login" element={<LoginSite />} />
          <Route path="signup" element={<SignUpSite />} />
        </Routes>
      </CartProvider>
    </AuthProvider>
  );
}

export default App;
