import React, { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./UserSidebar.module.css";
import { useCookies } from "react-cookie";
const UserNavItem = [
  {
    name: "Ti khon ca ti",
    path: "/user/profile",
    icon: "fa-solid fa-user",
    color: "rgb(25, 117, 198)",
    subNav: [
      {
        name: "H s",
        path: "/user/profile",
      },
      {
        name: "i mt khu",
        path: "/user/password",
      },
    ],
  },
  {
    name: "n mua",
    path: "/user/purchase",
    icon: "fa-solid fa-receipt",
    color: "rgb(0, 169, 185)"
  },
];

function UserSidebar() {
  const [cookies, setCookie] = useCookies("user");
  const [User, setUser] = useState(cookies.user);

  return (
    User?<div className={styles.container}>
    <div className={styles.avatar_container}>
      <div className={styles.avatar_wrapper}>
        <img src={User.USER_IMAGE} alt="nh i din" />
      </div>
      <div className={styles.name_wrapper}>
        <span className={styles.name}>{User.USER_NAME}</span>
      </div>
    </div>
    <div className={styles.horizontal}></div>
    <div className={styles.user_nav_container}>
      {UserNavItem.map((Nav) => (
        <div className={styles.nav_item} key={Nav.name}>
          <Link
            to={`${Nav.path}`}
            className={styles.nav_item_link}
          >
            <span className={styles.nav_logo} style={{color: Nav.color}}>
              <i className={Nav.icon}></i>
            </span>
            <span className={styles.nav_item_name}>{Nav.name}</span>
            
          </Link>
          {Nav.subNav && (
              <div className={styles.subnav_container}>
                {Nav.subNav.map((SubNav) => (
                  <Link 
                    to={`${SubNav.path}`}
                    key={SubNav.name}
                    className={styles.nav_item_link}
                  >
                    <span className={styles.nav_item_name}>
                      {SubNav.name}
                    </span>
                  </Link>
                ))}
              </div>
            )}
        </div>
      ))}
    </div>
  </div>:null
  );
}

export default UserSidebar;
