import React, { memo } from "react";

import styles from "./DeletedProductsStorage.module.css";

function DeletedProductsStorage({
  Products: DeletedProducts,
  CancelDeleteProduct,
  handleCloseStorage
}) {

  return (
    <div className={styles.modal}>
      <div className={styles.modal_container}>
        <div className={styles.modal_header}>
          <div className={styles.hero_title}>Sn phm  xo</div>
        </div>
        <div className={styles.modal_body}>
          <table className={styles.table}>
            <thead>
              <tr>
                <th>#</th>
                <th>Tn sn phm</th>
                <th>nh</th>
                <th>Gi</th>
                <th>Ngy to</th>
                <th>Ngy xo</th>
                <th>Tu chn</th>
              </tr>
            </thead>
            <tbody>
              {DeletedProducts.map((Product) => (
                <tr>
                  <td>{Product.PRODUCT_ID}</td>
                  <td>
                      <div className={styles.product_name}>
                        {Product.PRODUCT_NAME}
                      </div>
                  </td>
                  <td>
                    <img
                      src={Product.PRODUCT_IMAGE}
                      alt=""
                      className={styles.product_image}
                    />
                  </td>
                  <td>
                    {Number.parseFloat(Product.PRODUCT_PRICE).toLocaleString()}
                  </td>
                  <td>{Product.PRODUCT_CREATE_AT}</td>
                  <td>{Product.PRODUCT_DELETE_AT}</td>
                  <td>
                    <div
                      className={styles.product_controller}
                      onClick={() => CancelDeleteProduct(Product.PRODUCT_ID)}
                    >
                      Hu
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <div className={styles.modal_controller}>
            <button className="btn" onClick={() => handleCloseStorage()}>Tr v</button>
        </div>
      </div>
    </div>
  );
}

export default memo(DeletedProductsStorage);
