import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import styles from "./home.module.css";
import { useCookies } from 'react-cookie'
import Loading from "../../components/Loading";
import Slider from "../../components/Slider/Slider";
import ProductSlider from "../../components/DynamicSlider/Slider";
import Products from "../../components/Products";
import axios from "axios";

axios.defaults.withCredentials = true;

const getCategoryList = (ProductList) => {
  const result = ProductList.reduce((acc, cur) => {
    if (!acc.find((category) => category === cur.category)) {
      acc.push(cur.category);
    }

    return acc;
  }, []);
  return result;
};

function Home() {
  const [products, setProducts] = useState();
  const [isLoading, setIsLoading] = useState(true);
  const [categories, setCategories] = useState([]);
  const [cookies, setCookies] = useCookies()
  const sliderSettings = {
    className: "slick-pane",
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 3,
  };

  useEffect(() => {
    setIsLoading(true);
    axios({
      method: "GET",
      url: "http://localhost/server/product.php",
    })
      .then((res) => {
        setIsLoading((prev) => !prev);
        const data =   res.data.PRODUCTS;
        setProducts(data.filter(product => product.IS_DISABLED === '0'));
        setCategories(getCategoryList(res.data.PRODUCTS));
      })
      .catch((error) => console.log(error));
  }, []);

  return (
    <div className={styles.section_container}>
      <div className="grid wide">
        <div className="row">
          <div className="col c-12">
            <div className={styles.container}>
              <Slider />
            </div>
            {/* <div className={styles.container}>
              <Shops />
            </div> */}

            <div className={styles.container}>
              <div className={styles.products_show}>
                <div className={styles.main_title}>
                  <span className={styles.title}>Sn phm ni bt</span>
                </div>
                <div className={styles.main_content}>
                  <div className={styles.product_slider}>
                    {isLoading ? (
                      <Loading />
                    ) : (
                      <ProductSlider
                        settings={sliderSettings}
                        data={products}
                        reference={'product'}
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>

            <div className={styles.container}>
              <div className={styles.products_show}>
                <div className={styles.main_title}>
                  <span className={styles.title}>Tt c sn phm</span>
                </div>
                <div className={styles.main_content}>
                  {isLoading ? (
                    <Loading />
                  ) : (
                    <>
                      <Products products={products} productsPerRow={6} />
                      <div className={styles.bottom_controller}>
                        <Link
                          to="products"
                          className={`btn ${styles.bottom_button}`}
                        >
                          Xem thm
                        </Link>
                      </div>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
