import React from 'react'

import Slider from "react-slick";

import styles from './slider.module.css'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const sliderImgs = [
  "/resources/img/slider/6024960.jpg",
  "/resources/img/slider/slider_img_2.jpg",
  "/resources/img/slider/slider_img_3.jpg",
  "/resources/img/slider/slider_img_4.png",
  "/resources/img/slider/slider_img_5.jpg"
]

function HomeSlider() {
  const settings = {
    autoplay: true,
    adaptiveHeight: true,
    dots: true,
    infinite: true,
    speed: 650,
    slidesToShow: 1,
    slidesToScroll: 1,
    className: styles.container
  };

  return (
    <div>
      <Slider {...settings}>
        {sliderImgs && sliderImgs.map((img, index) => (
          <div className={styles.item} key={index} >
            <img src={process.env.PUBLIC_URL + img} alt="" />
          </div>
        ))}
      </Slider>
    </div>
  )
}

export default HomeSlider