import { useEffect, useState } from "react";

import styles from "./HomeAdmin.module.css";

import Widget from "../../components/Widget/Widget";
import AdminOrders from "../../components/AdminOrders/Orders";
import axios from 'axios'

function Home() {
 const [Details, setDetails] = useState({})

  useEffect(() => {
    axios("http://localhost/server/dashboard.php")
      .then(response => {
        const result = response.data
        if(result.SUCCESS) {
          setDetails(result.DETAILS)
        }
      })
  }, [])

  return (
      Details && 
      <div className={styles.section_container}>
      <div className={styles.widget_container}>
        <Widget type="earnings" value={Details.PROFIT} />
        <Widget type="users" value={Details.CUSTOMER_COUNT} />
        <Widget type="orders" value={Details.NEW_ORDERS} />
        <Widget type="products" value={Details.PRODUCT_LENGTH} />
      </div>

      <div className={`${styles.recent_orders_container} ${styles.content_container}`} >
        <div className={styles.title}>n t hng gn y</div>
        <AdminOrders Orders={Details.RECENT_ORDERS} />
      </div>
    </div>
  );
}

export default Home;
