import { useState } from "react";
import { Outlet, useLocation, useNavigate, Navigate } from "react-router-dom";
import styles from "./Admin.module.css";
import { useCookies } from "react-cookie";
import Sidebar from "../../components/Sidebar/Sidebar";

function Admin({ roleAccepted }) {
  const [isHide, setIsHide] = useState(true);
  const [cookies, setCookie] = useCookies(["user"]);
  const location = useLocation();
  const navigate = useNavigate();
  const pathName = location.pathname;

  let HeroTitle;

  switch (pathName) {
    case "/admin":
      HeroTitle = "Trang ch";
      break;
    case "/admin/shop":
      HeroTitle = "Ca hng ca ti";
      break;
    case "/admin/users":
      HeroTitle = "Tt c ngi dng";
      break;
    case "/admin/invoices":
      HeroTitle = "Tt c ho n";
      break;
    case "/admin/orders":
      HeroTitle = "Cc n t hng";
      break;
    case "/admin/category":
      HeroTitle = "Danh mc sn phm";
      break;
    case "/admin/manufacturers":
      HeroTitle = "Nh phn phi";
  }

  const handleHideSidebar = () => {
    setIsHide((prev) => !prev);
  };

  return (
    <>
      {cookies.user && (
        <div className={styles.container}>
          <Sidebar
            handleHideSidebar={handleHideSidebar}
            isHide={isHide}
            user={cookies.user}
          />
          <div
            className={`${styles.section_container} ${
              isHide ? styles.sidebar_off : ""
            }`}
          >
            <div className={styles.content_container}>
              <div className={styles.hero_title_container}>
                <div className={styles.hero_title}>{HeroTitle}</div>
              </div>

                <Outlet />
              
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default Admin;
