import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { useCookies } from "react-cookie";
import styles from "./EditForm.module.css";

function EditForm({ setOnEditing, handleEditUserAddress }) {
  const [cookies, setCookies] = useCookies(["user"])

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    reset({
      keepValues: false
    })
  }, [])

  const onSubmit = data => {
    const newCookies = {...cookies.user, ...data}
    setCookies("user", newCookies, {path: "/"});
    handleEditUserAddress(data)
    setOnEditing(false)
  }

  const handleFocus = (e) => {
    const formWrapper = e.target.closest(`.${styles.form_wrapper}`);
    const label = formWrapper.querySelector(`.${styles.label}`);

    label.classList.add(`${styles.focus}`);
  };

  const handleBlur = (e) => {
    if (!e.target.value) {
      const formWrapper = e.target.closest(`.${styles.form_wrapper}`);
      const label = formWrapper.querySelector(`.${styles.label}`);

      label.classList.remove(`${styles.focus}`);
    }
  };

  return (
    <div
      className={`${styles.form_overlay} ${styles.display}`}
    >
      <div
        className={`${styles.container}`}
        onClick={(e) => e.stopPropagation()}
      >
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className={styles.informations}>
            <div className={styles.title}>a ch mi</div>
            <div className={styles.form_wrapper}>
              <div className={styles.input_wrapper}>
                <input
                  type="text"
                  onFocus={(e) => handleFocus(e)}
                  onBlur={(e) => handleBlur(e)}
                  {...register("USER_FULL_NAME", {
                    required: {
                      value: true,
                      message: "khng c b trng",
                    },
                    minLength: {
                      value: 8,
                      message: "khng hp l",
                    },
                  })}
                />
              </div>
              <label className={styles.label}>
                H v tn
              </label>
            </div>

            <div className={styles.form_wrapper}>
              <div className={styles.input_wrapper}>
                <input
                  type="text"
                  onFocus={(e) => handleFocus(e)}
                  onBlur={(e) => handleBlur(e)}
                  {...register("USER_PHONE_NUMBER", {
                    required: "khng c b trng",
                    pattern: {
                      value: /(((\+|)84)|0)(3|5|7|8|9)+([0-9]{8})\b/,
                      message: "khng hp l",
                    },
                  })}
                />
              </div>
              <label htmlFor="USER_PHONE_NUMBER" className={styles.label}>
                S in thoi
              </label>
            </div>

            <div className={styles.form_wrapper}>
              <div className={styles.input_wrapper}>
                <input
                  type="text"
                  onFocus={(e) => handleFocus(e)}
                  onBlur={(e) => handleBlur(e)}
                  {...register("USER_ADDRESS", {
                    required: {
                      value: true,
                      message: "khng c b trng",
                    },
                    minLength: {
                      value: 10,
                      message: "khng hp l",
                    },
                  })}
                />
              </div>
              <label className={styles.label}>
                a ch c th
              </label>
            </div>
          </div>
          {Object.keys(errors).length !== 0 && (
            <>
              <div className={styles.error_title}>Li</div>
              <ul className={styles.error_container}>
                {errors.USER_FULL_NAME && <li>H v tn: {errors.USER_FULL_NAME.message}</li>}
                {errors.USER_PHONE_NUMBER && <li>S in thoi: {errors.USER_PHONE_NUMBER.message}</li>}
                {errors.USER_ADDRESS && <li>a ch: {errors.USER_ADDRESS.message}</li>}
              </ul>
            </>
          )}
          <div className={styles.control_container}>
            <button
              type="button"
              className={`btn ${styles.sub_button}`}
              onClick={() => setOnEditing(false)}
            >
              Tr li
            </button>
            <button className={`btn btn--primary ${styles.primary_button}`}>
              Xc nhn
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default EditForm;
